/* eslint-disable no-undef */
import { $, getWindowSize } from '../utils'

const canvas = document.createElement('canvas')

const app = new PIXI.Application({
  width: 750,
  height: 1334,
  view: canvas,
  transparent: true,
  forceCanvas: true
})
export function loader() {
  // canvas.width = 750;
  // canvas.height = 1334;
  const { windowWidth } = getWindowSize()
  app.view.style.width = windowWidth + 'px'
  app.view.style.height = windowWidth / 750 * 1334 + 'px'
  app.view.style.bottom = 0

  $('.count-down-container')[0] && $('.count-down-container')[0].appendChild(app.view)

  return Promise.resolve()
}

export { app }
