import env from '@ali/lib-env'

let testing = env.params['testing']

const params = {}
// eslint-disable-next-line no-undef
if (testing && process.env.NODE_ENV === 'development') {
  const kv = testing.split(',')
  kv.forEach(i => {
    const v = i.split(':')
    params[v[0]] = v[1]
  })
}


export default params
