import env from '@ali/lib-env'
import assets from './assets'
import tester from '../common/libs/tester'
import * as request from '../common/libs/request'
import * as log from '../common/libs/log'

const {
  os,
  browser
} = env

const {
  isAndroid,
  isIOS,
} = os

const {
  isUC
} = browser

function trim(s) {
  return s.replace(/\s/g, '').toLowerCase()
}

function matchWhiteList(model) {
  log.debug('当前设备型号' + model)
  if (model == 'unkown') {
    return window.NINJA_BIZ.downgrade().then((data) => {
      return Promise.resolve(data)
    }, () => {
      return Promise.resolve()
    }).catch(() => {
      return Promise.resolve()
    })
  }

  const whitelist = assets.get('whitelist')

  let downgrade2D = true


  const [matched] = whitelist ? whitelist.filter(wl => trim(wl.model) === trim(model)) : []

  if (matched) {
    log.debug('matched 到了：' + matched.downgrade2D)
    downgrade2D = false
    downgrade2D = matched.downgrade2D === true ||
      matched.downgrade2D === 'true'
  }

  return Promise.resolve({
    downgrade2D
  })
}

function matchRule({ downgrade2D }) {
  if (typeof downgrade2D == 'undefined') {
    downgrade2D = false
  }

  if (!isAndroid && !isIOS) {
    return Promise.resolve({
      downgrade2D
    })
  }
  if (downgrade2D) {
    return Promise.resolve({
      downgrade2D
    })
  }
  let {
    android,
    ios: iosVersion
  } = assets.get('downgrade2DInfo')
  let {
    uc: ucVersion,
    webview: webviewVersion
  } = android
  ucVersion = ucVersion || '4.4.0'
  webviewVersion = webviewVersion || '6.0.0'
  iosVersion = iosVersion || '9.0.0'

  let version
  if (isAndroid) {
    version = isUC ? ucVersion : webviewVersion
  } else if (isIOS) {
    version = iosVersion
  }

  try {
    downgrade2D = os.version.lt(version)
  } catch (e) { }

  return Promise.resolve({
    downgrade2D
  })
}

function downgradeRouter() {
  let force2D = assets.get('force2D')
  if (force2D == true || force2D == 'true') {
    return Promise.resolve({ downgrade2D: true })
  }

  let force3D = assets.get('force3D')
  if (force3D == true || force3D == 'true') {
    return Promise.resolve({ downgrade2D: false })
  }

  return request.getModel()
    .then(matchWhiteList)
    .then(matchRule)
}

export default function downgrade() {
  return downgradeRouter()
    .then(({
      downgrade2D
    }) => {
      if (tester.is2d == 1) {
        assets.set('downgrade2D', true)
      } else if (tester.is3d == 1) {
        assets.set('downgrade2D', false)
      } else if (downgrade2D) {
        assets.set('downgrade2D', true)
      }
      return Promise.resolve()
    }, () => {
      log.error('降级出错了！')
      assets.set('downgrade2D', true)
      return Promise.resolve()
    })
    .catch(() => {
      log.error('降级出错了！')
      assets.set('downgrade2D', true)
      return Promise.resolve()
    })
}