import createRedpack from './Redpack'
import { redpackAnim } from './anim'
import { getRealPx } from './util'
import { getWindowSize } from '../common/uikit/ui/kit/utils'

export default function operate(Game) {
  Game.prototype.setTimer = function () {
    this.stoped = false
    let redPackTexture = this.redPackTexture
    this.timer = setInterval(() => {
      const rdm = (Math.random() * 0.6 + 0.2)
      const s = createRedpack({
        x: ~~(rdm * this.width),
        y: this.height,
        width: getRealPx(111),
        height: getRealPx(147),
        image: redPackTexture
      })
      this.redpacks.add(s)
      redpackAnim(s, getRandomPos())
    }, this.interval)
  }

  Game.prototype.drawLine = function () {
    if (this.linePoints.length > 15) {
      this.linePoints.splice(0, this.linePoints.length - 15)
    }
    if (this.lineClear) {
      this.linePoints.splice(0, 1)
    }
    if (this.linePoints.length == 0) {
      this.lineClear = false
    }

    this.ctx.strokeStyle = '#CEB96E'
    this.ctx.lineJoin = 'bevel'
    this.ctx.lineCap = 'round'

    this.ctx.lineWidth = 3

    this.ctx.beginPath()

    this.linePoints.forEach((pos, i) => {
      if (i == 0) {
        this.ctx.moveTo(...pos)
      } else {
        this.ctx.lineTo(...pos)
      }
    })
    this.ctx.stroke()
  }

  Game.prototype.draw = function () {
    this.redpacks.forEach((s) => {
      // console.log(this.stoped, s.anim)
      if (s.using && !s.cutted) {
        s.draw({ ctx: this.ctx })
      } else if (s.cutted) {
        s.drawPriece({ ctx: this.ctx })
      }
    })
    // const a = createRedpack()
    // a.draw({ ctx: this.ctx, x: 50, y: 50 });
  }
  Game.prototype.check = function (pos) {
    this.cutPos = null

    this.redpacks.forEach((s) => {

      if (s.using && !s.cutted && s.check(pos)) {
        this.cutPos = pos
        // return if cut
        s.cut()
      }
    })
  }
  Game.prototype.clear = function () {
    this.ctx.clearRect(0, 0, this.width, this.height)
  }
}

function getRandomPos() {
  let { windowWidth, windowHeight } = getWindowSize()

  return [~~(Math.random() * windowWidth), (Math.random() * 0.3 + 0.1) * windowHeight]
}
