/* eslint-disable no-undef */
import Animation from './Animation'

export default class Spark {

  constructor(scene, count, crowd, parent, image) {
    let texture = new THREE.Texture(image)
    texture.needsUpdate = true

    this.mesh = new Animation(count, crowd, parent, texture)
    scene.add(this.mesh)
  }

  explode(index, explodeTime) {
    if (this.mesh.explodes[index - 1] === 0) {
      this.mesh.explodes[index - 1] = explodeTime
    }
  }

  stop(i) {
    if (this.mesh) {
      this.mesh.time[i] = 0
      this.mesh.explodes[i] = 0
    }
  }

  update(t, i) {
    if (this.mesh) {
      this.mesh.time[i] = t
    }
  }

}
