import operate from './operate'
import event from './event'
import state from '../app/data/state'
import { createLogoRedpack } from './Redpack'
import constant from '../app/data/constant'

export default class Game {
  /**
   * start
   * running
   * lottery
   * setProcess
   * getMute
   * getRhythm
   */
  constructor(resources) {
    const canvas = document.querySelector('#EleRedPacketNinjaCanvas3d')
    const windowWidth = window.innerWidth
    const windowHeight = window.innerHeight
    canvas.style.width = windowWidth + 'px'
    canvas.style.height = windowHeight + 'px'
    canvas.width = windowWidth
    canvas.height = windowHeight

    this.redPackTexture = resources.images.redPack
    !constant.hidePackLogo && createLogoRedpack(resources.images)
    this.container = document.querySelector('#EleRedPacketNinja')
    this.canvas = canvas
    this.ctx = canvas.getContext('2d')
    this.width = windowWidth
    this.height = windowHeight
    this.redpacks = new Set()
    this.linePoints = [] // 刀光
    this.lineClear = false
    this.originInterval = 300
    this.speedup = state.speedup
    this.stoped = true
    if (state.speedup == 1) {
      this.interval = this.originInterval
    } else {
      this.interval = 100
    }
    this.bindEvent()
  }

  update() {

    if (state.speedup !== this.speedup) {
      this.timer && clearInterval(this.timer)
      this.timer = null

      if (state.speedup == 1) {
        this.interval = this.originInterval
      } else {
        this.interval = 100
      }
      !this.stoped && !this.timer && this.setTimer()
      this.speedup = state.speedup
    }


    // 每帧更新动画
    this.clear()

    this.draw()
    this.drawLine()

    let redPackets = this.cutPos && [{ x: this.cutPos[0], y: this.cutPos[1] }]
    this.cutPos = null
    return redPackets
  }

  start() {
    // 一般用来初始化游戏逻辑，比如WebGL中创建context也可能导致crash，在这个回调中执行代码就可以避免这种crash
    this.setTimer()
  }

  sleep() {
    this.timer && clearInterval(this.timer)
    this.timer = null
  }

  active() {
    !this.stoped && !this.timer && this.setTimer()
  }

  stop() {
    this.stoped = true
    this.timer && clearInterval(this.timer)
    this.timer = null
  }
}

operate(Game)
event(Game)
