import leftPad from 'left-pad'
import raf from 'raf'
import { os } from '@ali/lib-env'
import * as utils from './utils'
import state from '../../../../app/data/state'
const { $, delay, getWindowSize } = utils

const config = {
  startTime: 0,
  running: true,
}
let handle
function setTime(time) {
  if (time < 0) {
    time = 0
  }
  const min = ~~(time / 60) % 10
  let sec = leftPad((time % 60).toString(), 2, '0')
  const str = min ? `${min}:${sec}` : sec
  $('#timeStr').text(str)
}

export function timeStart(gameTime) {

  let width = getWindowSize().windowWidth
  if (width > 540) {
    width = 540
  }
  if (!os.isIOS) { // 解决 部分 Android 倒计时频闪
    const scale = 50 / 750 * width / 10
    $('#timeStr')[0].style.transform = `scale(${scale})`
    $('#timeStr')[0].style.webkitTransform = `scale(${scale})`
    $('#timeStr')[0].style.marginTop = 50 / 2 / 750 * width + 'px'
  } else {
    $('#timeStr')[0].style.fontSize = 50 / 750 * width + 'px'
  }
  return new Promise((resolve) => {
    gameTime = gameTime || 30
    gameTime++
    let endTime = + Date.now() / 1000 + gameTime
    let lastTime = 1
    let leftTime = gameTime
    handle = raf(function tick() {

      if (config.running) {
        let now = Date.now() / 1000
        leftTime = state.stop ? -1 : (endTime - now) >> 0
        // console.log('leftTime: ', leftTime)
        // console.log('endTime: ', new Date() / 1000)
        // console.log('config.running: ', config.running)
        // console.log('gameTime:', gameTime)
        // console.log('=================')
        if (leftTime < 0) {
          raf.cancel(handle)
          return resolve()
        }

        if (lastTime && now - lastTime >= 1) {
          setTime(leftTime)
          lastTime = now
        }
      } else {
        endTime = Date.now() / 1000 + leftTime
      }

      handle = raf(tick)
    })
  })

}
export function timePause() {
  config.running = false
}
export function timePlay() {
  config.running = true
}
export function timeStop() {
  raf.cancel(handle)
}