/* eslint-disable no-undef */
import { opacityAnim } from './anim'

let config = {
  graphic: null,
  text: null,
  app: null
}

let textConf = {
  fontSize: 33,
  fontFamily: 'Arial',
  fill: '#FFFFFF',
  align: 'center',
}

let options = {}
export default function loading(opt, app) {
  options = opt
  config.app = app
  config.graphic = new PIXI.Graphics()
  config.graphic.alpha = 0
  textConf.fill = opt.progressTextColor || '#FFFFFF'
  textConf.fontSize = 42
  textConf.dropShadow = true
  textConf.dropShadowColor = 'rgba(0,0,0,.6)'
  textConf.dropShadowBlur = 10
  textConf.dropShadowAngle = Math.PI / 6
  textConf.dropShadowDistance = 6
  config.text = new PIXI.Text('0%', textConf)
  config.text.x = opt.progressPosition && opt.progressPosition.x || 375
  config.text.y = opt.progressPosition && opt.progressPosition.y || 720
  config.text.anchor.set(0.5)
  config.text.alpha = 0

  app.stage.addChild(config.graphic)
  app.stage.addChild(config.text)
  opacityAnim(config.graphic)
  opacityAnim(config.text)
}



