import PxLoader from 'pxloader'
import {Howl} from 'howler'

/**
 * @class PxLoaderAudio
 * 
 * import Howler.js to working with audio in JavaScript easy and reliable across all platforms.
 */
function PxLoaderAudio(name, tags, priority) {
	let self = this
	let loader = null

	this.tags = tags
	this.priority = priority

	this.audio = new Howl({
		src: [name],
		autoplay: false,
		loop: false,
		volume: 1,
		onload: () => {
			loader.onLoad(self)
		},
		onend: () => {
			this.onendcallback && this.onendcallback()
		},
		onloaderror: () => {
			loader.onError(self)
		}
	})

	this.start = (l) => {
		loader = l
	}

	this.checkStatus = () => {}

	this.onTimeout = () => {
		loader.onTimeout(self)
	}

	this.getName = function() {
		return name
	}
}

PxLoader.prototype.addAudio = function(url, tags, priority) {
	let audio = new PxLoaderAudio(url, tags, priority)

	this.add(audio)

	return audio.audio
}

export default PxLoaderAudio
