/* eslint-disable no-undef */

let sprites = {}
let opt = {}
export function startCreator(o) {
  opt = o
  const image = new Image()
  image.src = opt.background
  sprites['background.png'] = new PIXI.Texture(new PIXI.BaseTexture(image))
}


export function backgroundPic() {
  const pic = new PIXI.Sprite(sprites['background.png'])
  pic.alpha = 0
  pic.anchor.x = 0.5
  pic.anchor.y = 1
  pic.x = 375
  pic.y = 1336
  return pic
}

const ctx = document.createElement('canvas').getContext('2d')

export function numberPic(num) {

  var gradient = ctx.createLinearGradient(0, 0, 0, 120)
  gradient.addColorStop(0, opt.numberColor && opt.numberColor.start || '#fe6b33')
  gradient.addColorStop(1, opt.numberColor && opt.numberColor.end || '#ff218d')
  const text = new PIXI.Text(num.toString(), {
    fontWeight: 'bold',
    fontSize: 132,
    fontFamily: 'Arial',
    fill: gradient,
    align: 'center',
    stroke: 'rgba(15, 39, 219, 0.7)',
    strokeThickness: 8
  })
  text.alpha = 0
  text.anchor.x = 0.5
  text.anchor.y = 0.5
  text.x = opt.numberPosition && opt.numberPosition.x || 375
  text.y = opt.numberPosition && opt.numberPosition.y || 720
  return text
}
