/* eslint-disable no-undef */
import constant from '../../../app/data/constant'

export default function Animation(count, crowd, parent, texture) {
  let { velocities, startTimes } = parent
  let n = count * crowd
  let geometry = new THREE.BAS.PointBufferGeometry(n)
  geometry.createAttribute('explodeVelocity', 3)
  geometry.createAttribute('pointColor', 3)
  geometry.createAttribute('velocity', 3)
  geometry.createAttribute('key', 1)
  geometry.createAttribute('startPosition', 3)
  geometry.createAttribute('startTime', 1)
  let randFloat = THREE.Math.randFloat
  let { redPacketStartPosition, sparkMaxSpeed } = constant

  for (let i = 0; i < n; i++) {
    let j = (i / count) >> 0

    let ev = [
      randFloat(-0.5, 0.5) * sparkMaxSpeed,
      randFloat(-0.5, 0.5) * sparkMaxSpeed,
      randFloat(-0.5, 0.5) * sparkMaxSpeed
    ]

    geometry.setPointData('explodeVelocity', i, ev)

    let color = [
      randFloat(0, 1),
      randFloat(0, 0.7), // less green, because of the theme color
      randFloat(0, 1)
    ]

    geometry.setPointData('pointColor', i, color)

    geometry.setPointData('startPosition', i, redPacketStartPosition)
    geometry.setPointData('startTime', i, startTimes[j])
    geometry.setPointData('key', i, [j + 1])
  }

  let material = new THREE.BAS.PointsAnimationMaterial({
    shading: THREE.FlatShading,
    vertexColors: THREE.VertexColors,
    // blending: THREE.AdditiveBlending,
    transparent: true,
    uniforms: {
      uTime: { type: 'float', value: new Float32Array(crowd) },
      acceleration: { type: 'vec3', value: new THREE.Vector3(0, constant.redPacketAcceleration.y, 0) },
      explodes: { type: 'float', value: new Float32Array(crowd) },
      velocities: { type: 'vec3', value: velocities }
    },
    uniformValues: {
      map: texture,
      size: constant.sparkSize,
      sizeAttenuation: true
    },
    vertexFunctions: [
    ],
    vertexParameters: [
      `
			uniform vec3 acceleration;
			attribute vec3 startPosition;
			attribute vec3 pointColor;
			attribute float startTime;
			`,
      'uniform float uTime[' + crowd + '];',
      'uniform float explodes[' + crowd + '];',
      'uniform vec3 velocities[' + crowd + '];',
      `
			attribute vec3 explodeVelocity;
			attribute float key;
			`
    ],
    vertexPosition: [
      `
			vUv = vec2(0.5, 0.5);

			int i = int(key) - 1;
			float t = uTime[i];
			t = clamp(t, 0.0, 1.0);

			vec3 velocity = velocities[i];
			vec3 v = velocity * t;
			float explodeTime = explodes[i];
			float explodeElapsedTime = t - explodeTime;

			if (explodeTime > 0.0) {
				v += explodeVelocity * explodeElapsedTime;
			}

			t = clamp(t, 0.0, 1.0);
			vProgress = explodeElapsedTime;
			transformed *= 1.0 - t;
			transformed += startPosition + v + acceleration * t * t;
			vPointColor = pointColor;
			`
    ],
    varyingParameters: [
      `
			varying float vProgress;
			varying vec3 vPointColor;
			varying vec2 vUv;
			`
    ],
    fragmentDiffuse: [
      `
			diffuseColor.rgb *= vPointColor.rgb;
			diffuseColor.a = 1.0 - vProgress;
			`
    ]
  })

  THREE.Points.call(this, geometry, material)
}

Animation.prototype = Object.create(THREE.Points.prototype)
Animation.prototype.constructor = Animation

Object.defineProperty(Animation.prototype, 'time', {
  get: function () {
    return this.material.uniforms['uTime'].value
  },
  set: function (v) {
    this.material.uniforms['uTime'].value = v
  }
})

Object.defineProperty(Animation.prototype, 'explodes', {
  get: function () {
    return this.material.uniforms['explodes'].value
  },
  set: function (v) {
    this.material.uniforms['explodes'].value = v
  }
})

Object.defineProperty(Animation.prototype, 'velocities', {
  get: function () {
    return this.material.uniforms['velocities'].value
  },
  set: function (v) {
    this.material.uniforms['velocities'].value = v
  }
})

Object.defineProperty(Animation.prototype, 'rotations', {
  get: function () {
    return this.material.uniforms['rotations'].value
  },
  set: function (v) {
    this.material.uniforms['rotations'].value = v
  }
})
