import env from '@ali/lib-env'
import semver from 'semver'

let {
  os,
  aliapp
} = env

const {
  isIOS
} = os

const OS_NAME = isIOS ? 'iOS' : 'Android'
const APP_NAME = aliapp && aliapp.appname || 'unkown'
const APP_VERSION = aliapp && aliapp.version || '0.0.0'


function compareVersion(source, target) {
  source = semver.clean(source)
  return semver.satisfies(source, target)
}

function matching(map) {
  const appKey = `${OS_NAME}-${APP_NAME}`
  return map.has(appKey)
}

function get(
  map,
  keySet,
  osName = OS_NAME,
  appName = APP_NAME,
  appVersion = APP_VERSION,
  name
) {
  appVersion = appVersion.toString()

  const appKey = `${osName}-${appName}`

  if (!map.has(appKey)) {
    return ''
  }

  const appMap = map.get(appKey)

  let lastMatchedVersion
  if (appMap.has(appVersion)) {
    lastMatchedVersion = appVersion
  } else if (appMap.has(`=${appVersion}`)) {
    lastMatchedVersion = `=${appVersion}`
  } else {
    for (const version of appMap.keys()) {
      // 6.0.0 compare to >=5.11.0
      // 6.0.0 compare to <=7
      if (compareVersion(appVersion, version)
        && (
          !lastMatchedVersion
          // >=6.0.0 compare to >=5.0.0
          // <=4.0.0 compare to <=5.0.0
          // 3 compare to <=5
          // 7 compare to >=4
          || compareVersion(version, lastMatchedVersion)
        )
      ) {
        lastMatchedVersion = version
      }
    }
  }

  if (lastMatchedVersion) {
    return appMap.get(lastMatchedVersion).get(name)
  }
}

function set(
  map,
  keySet,
  osName = OS_NAME,
  appName = APP_NAME,
  appVersion = APP_VERSION,
  name,
  value
) {
  appVersion = appVersion.toString()

  const appKey = `${osName}-${appName}`

  let appMap
  if (!map.has(appKey)) {
    appMap = new Map()
    map.set(appKey, appMap)
  } else {
    appMap = map.get(appKey)
  }

  let kvMap
  if (!appMap.has(appVersion)) {
    kvMap = new Map()
    appMap.set(appVersion, kvMap)
  } else {
    kvMap = appMap.get(appVersion)
  }

  keySet.add(name)
  kvMap.set(name, value)
}

function createByVersion(ctx, version) {
  return new VersionableConfig(ctx.root, ctx._osName, ctx._appName, version)
}

function createByOS(ctx, os) {
  return new VersionableConfig(ctx.root, os, ctx._appName)
}

function createByApp(ctx, app) {
  return new VersionableConfig(ctx.root, ctx._osName, app)

}

function passContext(ctx) {
  return [ctx.map, ctx.keySet, ctx._osName, ctx._appName, ctx._appVersion]
}

/**
 * @example
 * 
 * config
 *     .set(name, value) // default value
 *     .isIOS            // specified platform
 *     .set(name, value) // default value with platform
 *     .gt('5.11').set(name, value1) // >=5.12.0
 *     .gte('6.0.2').set(name, value2) //  >=6.0.2
 *     .isAndroid
 *     .is('6.1 - 7').set(name, value3); // >=6.1 <8
 *     
 *     
 * config.get(name); // match with current platform and appVersion
 *
 * config.isIOS.get(name); // match with specified platform
 *
 * config.isAndroid.eq('6.2.0').get(name) // match with specified platform & appVersion
 */
export default class VersionableConfig {
  constructor(root, osName, appName, appVersion) {
    if (root) {
      this._root = root
      this._osName = osName
      this._appName = appName
      this._appVersion = appVersion
    } else {
      this._map = new Map()
      this._keys = new Set()
    }
  }

  get map() {
    return this._map || this._root && this._root._map
  }

  get keySet() {
    return this._keys || this._root && this._root._keys
  }

  get root() {
    return this._root || this
  }

  get(name) {
    return get(...passContext(this), name)
  }

  set(name, value) {
    set(...passContext(this), name, value)
    return this
  }

  keys() {
    return this.keySet.keys()
  }

  matching() {
    if (matching(this.map)) {
      return this
    }
  }

  get isIOS() {
    return createByOS(this, 'iOS')
  }

  get isAndroid() {
    return createByOS(this, 'Android')
  }

  is(appName) {
    return createByApp(this, appName)
  }

  get isTB() {
    return this.is('TB')
  }

  get isTM() {
    return this.is('TM')
  }

  gt(appVersion) {
    return createByVersion(this, `>${appVersion}`)
  }

  gte(appVersion) {
    return createByVersion(this, `>=${appVersion}`)
  }

  lt(appVersion) {
    return createByVersion(this, `<${appVersion}`)
  }

  lte(appVersion) {
    return createByVersion(this, `<=${appVersion}`)
  }

  eq(appVersion) {
    return createByVersion(this, `=${appVersion}`)
  }
}