import View from './view/View'
import Control from './control/Control'
import state from '../app/data/state'

export default class Game {
  constructor(resources) {
    this.view = new View(resources)

    const { camera, renderer, scene } = this.view.engine

    this.control = new Control(camera, renderer, scene)

    this.control.touch.addObjects(this.view.redPacket.meshForPicker)

    // clear the sword points when touchstart
    this.control.touch.on('start', (e) => {
      this.view.sword.start({
        x: e.x,
        y: e.y,
      })
    })

    this.control.touch.on('mouseenter', (e) => {
      this.view.sword.start({
        x: e.x,
        y: e.y,
      })
    })
  }

  update(delta) {
    if (!this.running) {
      return
    }

    this.control.update(delta)

    state.intersections = this.control.intersections
    state.mouse = this.control.touch.mouse

    const { cutVector } = this.control.touch
    state.cutVector.x = cutVector.y
    state.cutVector.y = cutVector.x
    state.cutVector.z = 0
    state.cutVector.normalize()

    // the explode state is export from view
    const { redPackets } = this.view.update(delta)
    // console.log(redPackets)
    if (redPackets) {
      return redPackets.map((redPacket, i) => {
        return redPacket && this.control.touch.mouses[i]
      })
    }
  }

  sleep() {
    this.view.sleep()
  }

  active() {
    this.view.active()
  }

  start() {
    this.running = true
    this.view.start()
    this.control.start()
  }

  stop() {
    this.view.stop()
    this.control.stop()
  }

  clear() {
    this.view.engine.renderer.clear()
  }
}
