/* eslint-disable no-undef */
import GPUPicker from './GPUPicker'
import CPUPicker from './CPUPicker'
import ee from 'event-emitter'
import constant from '../../app/data/constant'

export default class Touch {

  constructor(container, camera, renderer, isMobile) {
    this.isMobile = isMobile
    this.renderer = renderer
    this.camera = camera
    this.container = container

    if (constant.redPacketUseGPUPicker) {
      this.picker = new GPUPicker({
        renderer: renderer,
        scale: 0.1,
        camera: camera
      })
    } else {
      this.picker = new CPUPicker({
        camera: camera
      })
    }

    // default cut vector for just touchstart, no touchmove
    this.cutVector = new THREE.Vector2(0, 1)

    this.camera = camera

    // prevent the default browser behavior in pc
    if (!isMobile) {
      document.onselectstart = function () {
        return false
      }

      document.onmousedown = function () {
        return false
      }
    }

    this.tmpMouse = new THREE.Vector2()

    // interpolate the points to cut more easily
    this.interpolatedMouse = new THREE.Vector2()
    this.mouses = []

    this.TOUCHSTART = isMobile ? 'touchstart' : 'mousedown'
    this.TOUCHMOVE = isMobile ? 'touchmove' : 'mousemove'
    this.TOUCHEND = isMobile ? 'touchend' : 'mouseup'

    // for PC and Mobile
    this.touchMove = this.touchMove.bind(this)

    // for Mobile
    this.touchStart = this.touchStart.bind(this)
    this.touchEnd = this.touchEnd.bind(this)

    // for PC
    this.mouseLeave = this.mouseLeave.bind(this)
    this.mouseEnter = this.mouseEnter.bind(this)

  }

  mouseLeave() {
    this.emit('mouseleave')
  }

  mouseEnter(event) {
    if (!this.mouse) {
      this.mouse = new THREE.Vector2()
    }

    this.mouse.x = event.clientX
    this.mouse.y = event.clientY

    this.emit('mouseenter', this.mouse)
  }

  touchStart(event) {
    this.touching = true

    let e = this.isMobile ? event.changedTouches[0] : event

    // FIXED: define the the mouse here, not in constructor
    if (!this.mouse) {
      this.mouse = new THREE.Vector2()
    }

    this.mouse.x = e.clientX
    this.mouse.y = e.clientY

    this.mouses = [this.mouse]

    // update gpuPicker when touch start
    this.picks = this.picker.update(this.mouses)

    this.emit('start', this.mouse)
  }

  touchMove(event) {
    // prevent default event handler which may cause some performance issue
    event.preventDefault()

    if (this.isMobile) {
      if (!this.touching) {
        return
      }
    } else {
      if (!this.mouse) {
        this.mouse = new THREE.Vector2()
      }
    }

    let e = this.isMobile ? event.changedTouches[0] : event

    let x = this.tmpMouse.x = e.clientX
    let y = this.tmpMouse.y = e.clientY

    if (this.mouse) {
      this.interpolatedMouse.x = (this.tmpMouse.x + this.mouse.x) * 0.5
      this.interpolatedMouse.y = (this.tmpMouse.y + this.mouse.y) * 0.5

      this.cutVector.addVectors(this.tmpMouse, (this.mouse.negate()))
      this.mouses = [this.tmpMouse, this.interpolatedMouse]
    }

    // update gpuPicker when touch move
    this.picks = this.picker.update(this.mouses)

    this.mouse.x = x
    this.mouse.y = y

  }

  touchEnd(event) {
    let e = this.isMobile ? event.changedTouches[0] : event

    this.emit('end', e)
    this.touching = false
  }

  start() {
    let { container } = this

    container.addEventListener(this.TOUCHMOVE, this.touchMove, false)

    if (this.isMobile) {
      // bind the touch event on container, so the UI element won't disturb the touch event
      container.addEventListener(this.TOUCHSTART, this.touchStart, false)
      container.addEventListener(this.TOUCHEND, this.touchEnd, false)
    } else {
      container.addEventListener('mouseleave', this.mouseLeave, false)
      container.addEventListener('mouseenter', this.mouseEnter, false)
    }
  }

  stop() {
    let { container } = this

    container.removeEventListener(this.TOUCHMOVE, this.touchMove)


    if (this.isMobile) {
      container.removeEventListener(this.TOUCHSTART, this.touchStart)
      container.removeEventListener(this.TOUCHEND, this.touchEnd)
    } else {
      container.removeEventListener('mouseleave', this.mouseLeave, false)
      container.removeEventListener('mouseenter', this.mouseEnter, false)
    }

    // FIXED: the game may be stopped suddenly when touchmove
    this.touching = false
  }

  render() {
    this.gpuPicker.render()
  }

  addObjects(objects) {
    this.picker.addObjects(objects)
  }

  update() {
    // test if the framebuffer is correct
    // this.render()
    if (this.isMobile) {
      if (this.touching) {
        return this.picks
      }
    } else {
      return this.picks
    }
  }

}

ee(Touch.prototype)
