/* eslint-disable no-undef */
/**
 * @class GPUPicker
 * @author qianwei.xqw
 *
 * inspired by https://github.com/brianxu/GPUPicker/blob/master/GPUPicker.js
 * But it's much simpler than that, just for pure color picking
 */
export default class GPUPicker {
  constructor(option) {
    if (option === undefined) {
      option = {}
    }

    this.pickingScene = new THREE.Scene()
    this.pickingTexture = new THREE.WebGLRenderTarget()
    this.pickingTexture.texture.minFilter = THREE.LinearFilter
    this.pickingTexture.texture.generateMipmaps = false


    this.scale = option.scale || 1

    if (option.renderer) {
      this.setRenderer(option.renderer)
    }

    if (option.camera) {
      this.setCamera(option.camera)
    }
  }

  render() {
    this.renderer.render(this.pickingScene, this.camera)
  }

  update(mouses) {
    let { renderer, pickingTexture, pixelBuffer } = this

    // FIXED：must clear target every frame
    renderer.clearTarget(pickingTexture)
    renderer.render(this.pickingScene, this.camera, pickingTexture)

    let { width, height } = pickingTexture

    // read the rendering texture
    renderer.readRenderTargetPixels(pickingTexture, 0, 0, width, height, pixelBuffer)

    return mouses.map((mouse) => {
      // FIXED: the mouse position must be int, not float
      let index = (((mouse.x * this.scale) >> 0) + (height - ((mouse.y * this.scale) >> 0)) * width)

      // the red channel of color records the key
      return pixelBuffer[index * 4]
    })

  }

  setCamera(camera) {
    this.camera = camera
  }

  setRenderer(renderer) {
    this.renderer = renderer
    var size = renderer.getSize()
    this.resizeTexture(size.width * this.scale >> 0, size.height * this.scale >> 0)
  }

  resizeTexture(width, height) {
    this.pickingTexture.setSize(width, height)
    this.pixelBuffer = new Uint8Array(4 * width * height)
  }

  setScene(scene) {
    this.pickingScene = scene.clone()
  }

  addObjects(objects) {
    let { pickingScene } = this

    for (let i = 0, len = objects.length; i < len; i++) {
      pickingScene.add(objects[i])
    }
  }
}

