/* eslint-disable no-undef */
export default function Animation(geometry, material) {
  THREE.Mesh.call(this, geometry, material)
}

Animation.prototype = Object.create(THREE.Mesh.prototype)
Animation.prototype.constructor = Animation

Object.defineProperty(Animation.prototype, 'time', {
  get: function () {
    return this.material.uniforms['uTime'].value
  },
  set: function (v) {
    this.material.uniforms['uTime'].value = v
  }
})

Object.defineProperty(Animation.prototype, 'explodes', {
  get: function () {
    return this.material.uniforms['explodes'].value
  },
  set: function (v) {
    this.material.uniforms['explodes'].value = v
  }
})

Object.defineProperty(Animation.prototype, 'velocities', {
  get: function () {
    return this.material.uniforms['velocities'].value
  },
  set: function (v) {
    this.material.uniforms['velocities'].value = v
  }
})

Object.defineProperty(Animation.prototype, 'explodeVelocities', {
  get: function () {
    return this.material.uniforms['explodeVelocities'].value
  },
  set: function (v) {
    this.material.uniforms['explodeVelocities'].value = v
  }
})

Object.defineProperty(Animation.prototype, 'rotations', {
  get: function () {
    return this.material.uniforms['rotations'].value
  },
  set: function (v) {
    this.material.uniforms['rotations'].value = v
  }
})


Object.defineProperty(Animation.prototype, 'explodeRotations', {
  get: function () {
    return this.material.uniforms['explodeRotations'].value
  },
  set: function (v) {
    this.material.uniforms['explodeRotations'].value = v
  }
})
