/* eslint-disable no-undef */
import Animation from './Animation'
import Geometry from './Geometry'
import Material from './Material'
import Spark from '../Spark/Spark'
import constant from '../../../app/data/constant'

export default class RedPacket {

  constructor(scene, resources, redPacketImage) {
    this.scene = scene
    this.model = resources.model

    let { redPacketNumber, redPacketStartPosition } = constant
    let { x, y, z } = constant.redPacketVelocity
    let [startTimeMin, startTimeMax] = constant.redPacketStartTime

    let startPositions = []
    let startTimes = this.startTimes = []
    let velocities = this.velocities = []
    let rotations = this.rotations = []
    let explodeVelocities = [] // add the velocity when explode
    let explodeRotations = [] // add the velocity when explode
    let axisTmp = new THREE.Vector3()

    this.axisTmpForPicker = new THREE.Vector3()
    this.positions = []
    this.lives = new Float32Array(constant.redPacketNumber)
    // get the mesh from scene model
    let m0 = this.model.getObjectByName(constant.redPacketModelAboveId)
    let m1 = this.model.getObjectByName(constant.redPacketModelBelowId)

    let g0, g1

    // fromBufferGeometry hurts performance, so I modify the lib/three.bas's prefabGeometry to input bufferGeometry
    let isBufferGeometry = true

    if (isBufferGeometry) {
      g0 = m0.geometry
      g1 = m1.geometry
    } else {
      // convert bufferGeomtry to geometry
      g0 = new THREE.Geometry()
      g1 = new THREE.Geometry()

      g0.fromBufferGeometry(m0.geometry)
      g1.fromBufferGeometry(m1.geometry)
    }


    let keys = []

    for (let i = 0; i < redPacketNumber; i++) {
      startPositions[i] = new THREE.Vector3(redPacketStartPosition[0], redPacketStartPosition[1], redPacketStartPosition[2])

      startTimes[i] = [THREE.Math.randFloat(startTimeMin, startTimeMax)]

      this.reset(velocities, rotations, i, x, y, z, axisTmp)

      explodeVelocities[i] = new THREE.Vector3()
      explodeRotations[i] = 0

      keys[i] = [i + 1]
    }

    let texture = new THREE.Texture(redPacketImage)
    texture.needsUpdate = true

    function createMesh(geom, velocities, rotations) {
      let buffers = {
        keys: keys
      }

      let geometry = new Geometry(geom, buffers, redPacketNumber, isBufferGeometry)
      // geometry.computeVertexNormals()
      geometry.bufferUvs()

      // material for scene
      let material = new Material(false, texture, velocities, explodeVelocities, rotations, explodeRotations, startTimes, startPositions)

      return new Animation(geometry, material)
    }

    this.mesh0 = createMesh(g0, velocities, rotations)
    this.mesh1 = createMesh(g1, velocities, rotations)

    this.scene.add(this.mesh0)
    this.scene.add(this.mesh1)

    // set picker
    let { redPacketUseGPUPicker } = constant
    if (redPacketUseGPUPicker) {
      // first, use the cross section of red packet to decrease the gpu picker calculation
      let mForPicker = this.model.getObjectByName(constant.redPacketModelPickerId)

      let geometryForPicker = new THREE.Geometry()
      geometryForPicker.fromBufferGeometry(mForPicker.geometry)

      // material for raycaster
      let raycasterMaterial = new Material(true, undefined, velocities, explodeVelocities, rotations, explodeRotations, startTimes, startPositions)

      this.meshForGPUPicker = createMesh(geometryForPicker, velocities, rotations)

      this.raycasterMaterial = raycasterMaterial
      this.meshForGPUPicker.material = raycasterMaterial

      this.meshForPicker = [this.meshForGPUPicker]
    } else {
      // or use CPU picker
      this.meshForCPUPicker = []

      let geometryForPicker = new THREE.BoxGeometry(2, 3, 0.5)
      // TODO: opacity is 0 for pick, may cause some performance issue
      let material = new THREE.MeshBasicMaterial({
        opacity: 0,
        transparent: true
      })

      for (let i = 0; i < redPacketNumber; i++) {
        let mesh = new THREE.Mesh(geometryForPicker, material)
        mesh._key = keys[i]
        this.meshForCPUPicker.push(mesh)
        scene.add(mesh)
      }

      this.meshForPicker = this.meshForCPUPicker
    }

    // spark
    this.spark = new Spark(this.scene, constant.sparkNumber, constant.redPacketNumber, this, resources.images['spark'])

  }

  reset(velocities, rotations, i, x, y, z, axisTmp) {
    velocities[i] = new THREE.Vector3(
      THREE.Math.randFloat(x[0], x[1]),
      THREE.Math.randFloat(y[0], y[1]),
      THREE.Math.randFloat(z[0], z[1])
    )

    THREE.BAS.Utils.randomAxis(axisTmp)

    rotations[i] = new THREE.Vector4(
      axisTmp.x, axisTmp.y, axisTmp.z, Math.PI * 2
    )
  }

  explode(index, explodeTime, cutVector) {
    let isExplode = false
    let i = index - 1

    if (this.mesh0.explodes[i] === 0) {
      // set explodeTime which will be used to calculate the explode animation
      this.mesh0.explodes[i] = explodeTime
      this.mesh1.explodes[i] = explodeTime

      // add velocity to red packet when explode
      let v = constant.redPacketExplodeVelocity
      let r = constant.redPacketExplodeRotation
      this.mesh0.explodeVelocities[i] = new THREE.Vector3(cutVector.x * v, cutVector.y * v, 0)
      this.mesh1.explodeVelocities[i] = new THREE.Vector3(-cutVector.x * v, -cutVector.y * v, 0)

      let rotation = new THREE.Vector4(0, 0, -1, Math.atan2(cutVector.x, cutVector.y))
      this.mesh0.rotations[i] = rotation
      this.mesh1.rotations[i] = rotation

      // add rotation to red packet when explode
      this.mesh0.explodeRotations[i] = r
      this.mesh1.explodeRotations[i] = -r

      isExplode = true
    }

    if (isExplode) {
      this.spark.explode(index, explodeTime)
    }

    // return true means really explode
    return isExplode
  }

  update(delta, time, intersections, cutVector) {
    let { mesh0, mesh1, startTimes, lives, spark, raycasterMaterial, stopped, velocities, rotations, positions, meshForCPUPicker, axisTmpForPicker } = this

    let { redPacketLife, redPacketUseGPUPicker } = constant
    let explodeTimes = []
    let raycasterMaterialTime

    if (redPacketUseGPUPicker) {
      raycasterMaterialTime = raycasterMaterial.uniforms['uTime'].value
    }

    for (let i = 0, len = constant.redPacketNumber; i < len; i++) {
      // t is between 0 and 1
      let t = time - startTimes[i] * redPacketLife

      // life not begin
      if (t < 0) {
        continue
      }

      t = t / redPacketLife - (t / redPacketLife >> 0)

      // if stopped end the life
      if (stopped) {

        if (lives[i] >= 1) {
          spark.stop(i)
          continue
        }
        lives[i] += delta / redPacketLife

        // 强制清空
        t = lives[i]
      }

      // t < lives[i] means the life begins again

      if (t < lives[i]) {
        mesh0.explodes[i] = 0
        mesh1.explodes[i] = 0

        let { x, y, z } = constant.redPacketVelocity

        let axisTmp = new THREE.Vector3()

        this.reset(velocities, rotations, i, x, y, z, axisTmp)
        mesh0.velocities[i] = velocities[i]
        mesh1.velocities[i] = velocities[i]
        mesh0.rotations[i] = rotations[i]
        mesh1.rotations[i] = rotations[i]

        if (redPacketUseGPUPicker) {
          raycasterMaterial.uniforms['velocities'].value[i] = velocities[i]

          raycasterMaterial.uniforms['rotations'].value[i] = rotations[i]

        }

        spark.mesh.velocities[i] = velocities[i]
        spark.mesh.explodes[i] = 0
      }

      lives[i] = t

      // update mesh
      mesh0.time[i] = mesh1.time[i] = t

      if (redPacketUseGPUPicker) {
        raycasterMaterialTime[i] = t
      } else {
        // update picker
        this.pick(meshForCPUPicker, velocities, positions, rotations, i, t, axisTmpForPicker)
      }

      if (intersections) {
        intersections.forEach((intersection, j) => {
          if (intersection - 1 === i) {
            explodeTimes[j] = t
          }
        })
      }

      // update spark
      spark.update(t, i)
    }
    if (intersections) {
      return intersections.map((intersection, j) => {
        return this.explode(intersection, explodeTimes[j], cutVector)
      })
    }
  }

  /**
	 * CPU Pick
	 */
  pick(meshForCPUPicker, velocities, positions, rotations, i, t, axisTmpForPicker) {
    let { redPacketStartPosition, redPacketAcceleration } = constant

    // store the positions for pick
    if (!positions[i]) {
      positions[i] = new THREE.Vector3()
    }

    positions[i].x = redPacketStartPosition[0] + velocities[i].x * t
    positions[i].y = redPacketStartPosition[1] + velocities[i].y * t + redPacketAcceleration.y * t * t
    positions[i].z = redPacketStartPosition[2] + velocities[i].z * t

    axisTmpForPicker.x = rotations[i].x
    axisTmpForPicker.y = rotations[i].y
    axisTmpForPicker.z = rotations[i].z

    meshForCPUPicker[i].setRotationFromAxisAngle(axisTmpForPicker, rotations[i].w * t)

    meshForCPUPicker[i].scale.x = meshForCPUPicker[i].scale.y = meshForCPUPicker[i].scale.z = 4 + t * 2

    meshForCPUPicker[i].position.set(positions[i].x, positions[i].y, positions[i].z)
  }

  start() {
    this.stopped = false
  }

  stop() {
    this.stopped = true
  }
}
