import assets from './assets'
import uikit from '../common/uikit'
import * as series from './series'

let awardPopList = []

export default function (data) {

  const award = awardPopList.pop()
  if (award) {
    uikit.gainAward(award.type, data, assets.get('awardType'))
  }

  uikit.addCount()
  series.checkSeries(data)

  return window.NINJA_BIZ.lottery().then(data => {
    awardPopList.push(data)
    return Promise.resolve()
  }, () => {
    return Promise.resolve()
  }).catch(() => {
    return Promise.resolve()
  })
}