import * as AFT from '@ali/aft.js'
export function redpackAnim(s, pos) {
  const [x, y] = pos
  let targetX = s.x - ((s.x - x) << 1)

  const durationY1 = (1 - y / window.innerHeight) * 1500
  const durationY2 = durationY1 * 2 / 3


  const ax = x - s.x
  const ay = -(y - s.y)

  const tan = ax / ay
  const r = Math.atan(tan) * 180 / Math.PI
  s.rotate = r


  const anim1 = AFT.animate({
    y: s.y
  })
    .to({
      y: y
    })
    .duration(durationY1)
    .easing('ease-out')
    .update(curr => {
      s.y = curr.get('y')
    })
    .next()
    .to({
      y: s.y
    })
    .easing('ease-in')
    .duration(durationY2)
    .update(curr => {
      s.y = curr.get('y')
    })
    .play()
    .on('finish', () => {
      s.over()
    })

  const anim2 = AFT.animate({
    x: s.x
  }).to({
    x: targetX,
  })
    .duration(durationY1 + durationY2)
    .easing('ease-in')
    .update(curr => {
      s.x = curr.get('x')
    })
    .play()

  s.anim.push(anim1, anim2)
}

export function pieceAnim(s) {
  return new Promise(resolve => {

    const { type, x, y, rotate, height, alpha } = s


    const random = ~~(Math.random() * 10 + 30)


    const MoveY = ~~(Math.random() * height + height)
    const MoveX = ~~(Math.tan(rotate * Math.PI / 180) * MoveY)


    let targetRotate
    let targetX
    let targetY

    switch (type) {
    case 1:
      targetRotate = rotate > 0 ? rotate + random : rotate - random
      targetX = x + MoveX
      targetY = x - MoveY
      break
    default:
      targetRotate = rotate > 0 ? rotate - random : rotate + random
      targetX = x - MoveX
      targetY = x + MoveY
    }

    AFT.animate({
      x: x,
      y: y,
      rotate: rotate,
      alpha: alpha
    })
      .to({
        x: targetX,
        y: targetY,
        rotate: targetRotate,
        alpha: 0
      })
      .duration(800)
      .easing('ease-out')
      .update(curr => {
        s.x = curr.get('x')
        s.y = curr.get('y')
        s.rotate = curr.get('rotate')
        s.alpha = curr.get('alpha')
      })
      .play()
      .on('finish', () => {
        resolve()
      })
  })


}