export default function event(Game) {

  Game.prototype.bindEvent = function () {

    let moving = false

    const container = document.querySelector('#EleRedPacketNinja')

    container.addEventListener('touchstart', (e) => {
      if (this.stoped) return
      moving = true
      this.lineClear = false
      this.linePoints = []
      const point = [
        e.changedTouches[0].pageX,
        e.changedTouches[0].pageY
      ]
      this.linePoints.push(point)
      this.check(point)
    })

    container.addEventListener('touchmove', (e) => {
      e.preventDefault()
      if (this.stoped) return
      if (!moving) return
      const point = [
        e.touches[0].pageX,
        e.touches[0].pageY
      ]
      this.linePoints.push(point)
      if (!this.lineClear) {
        if (this.linePoints.length > 5) {
          this.lineClear = true
        }
        setTimeout(() => {
          this.lineClear = true
        }, 200)
      }
      this.check(point)

    })

    container.addEventListener('touchend', (e) => {
      if (this.stoped) return
      this.cutPos = null
      moving = false
      this.linePoints.push([e.changedTouches[0].pageX,
      e.changedTouches[0].pageY
      ])
      this.lineClear = true
    })
  }


}