import uikit from '../common/uikit'
import assets from './assets'
import * as log from '../common/libs/log'
import App from '../app/App'
import tester from '../common/libs/tester'
import { preload } from '../common/libs/loader'
import * as goldlog from '../common/libs/goldlog'
import lottery from './lottery'
import state from '../app/data/state'

let gameResolve
let rhythm = 0 // 0 正常 1 快速
let rhythmTimer = null
let app
export const exportObj = {}

function gameover() {
  goldlog.record('EXP', 'event=game_over&')
  const promise = () => new Promise((resolve) => {
    exportObj.restart = resolve
  })
  rhythm = 0
  gameResolve && gameResolve(promise)
}

export default function animation() {
  const importObject = {
    start() {
      log.debug('countdown start')

      // 在倒计时的时候 加载 游戏UI资源
      preload(assets.get('assets') || []).then(() => {
        log.debug('游戏UI资源加载完毕')
      })
      return new Promise((resolve, reject) => {

        goldlog.record('EXP', 'event=game_countdown_start&')
        uikit.countDown(assets.get('countdown'), assets.get('brandImage'))
          .then(() => window.NINJA_BIZ.gameStart())
          .then(() => {
            if (state.stop) {
              return
            }
            let gameTime = tester.gameTime ? parseInt(tester.gameTime) : parseInt(assets.get('gameTime'))
            gameTime = gameTime || 30
            log.debug('game start gameTime: ' + gameTime)

            uikit.start(gameTime).then(() => {
              log.debug('game over gameTime: ' + gameTime)
              uikit.hideGame()
              gameover()
              window.NINJA_BIZ.showResult(uikit.getCount())
            })

            // 设置游戏内部的logo
            uikit.initGame({
              seriesConfig: assets.get('series'),
              assets: assets
            })

            uikit.setAwardType(assets.get('awardType'))

            rhythmTimer && clearTimeout(rhythmTimer)
            rhythm = 0
            setTimeout(() => {
              // 设置 速率
              const speedupTime = assets.get('speedup')
              if (speedupTime) {
                rhythmTimer = setTimeout(() => {
                  rhythm = 1
                  log.debug('开始速率类型：1')
                }, parseInt(speedupTime) * 1000)
              }
              if (state.stop) {
                reject()
              } else {
                resolve()
              }
            }, 1000)
          }).catch((e) => {
            log.error(e)
            state.stop || resolve()
          })
      })


    },
    running() {
      log.debug('game animation start!')
      return new Promise(resolve => {
        gameResolve = resolve
      })
    },
    lottery(data) {
      return lottery(data).then((data) => {
        return data
      })
    },
    setProcess(num) {
      uikit.setProcess(num)
    },
    getMute() {
      return uikit.getMute()
    },
    setMute(...args) {
      return uikit.setMute(...args)
    },
    getRhythm() {
      return rhythm
    }
  }

  log.debug('instance game!')

  const config = Object.assign({},
    assets.get('countdownOption'),
    { cutAnim: assets.get('cutAnim') },
    { background: assets.get('background') })

  uikit.installEntry(config).then(() => {
    app = new App(importObject, assets.get('downgrade2D'))
  })

  return Promise.resolve()
}
// 添加暂停功能
export function pause() {
  app.pause()
  uikit.pause()
}


// 添加继续功能
export function play() {
  app.play()
  uikit.play()
}

export function stop() {
  state.stop = true
  app.stop()
  app.clear()
  uikit.uiStop()
  rhythm = 0
  const node = document.querySelector('#game-container')
  if (node) {
    const parentNode = node.parentNode ? node.parentNode.parentNode : null
    parentNode && parentNode.removeChild(node.parentNode)
  }

}

export function stopThenPlay() {
  // uikit.countDown(assets.get('countdown'), assets.get('brandImage'))
  // uikit.resetCache()

  const node = document.querySelector('.count-down-container')
  const parentNode = node.parentNode.parentNode
  parentNode.removeChild(node.parentNode)
  state.stop = false
  rhythm = 0
  play()
  // app.mute(uikit.getMute())
  animation()
}