import * as utils from './kit/utils'
import countTpl from './tpls/countDown.tpl'
import { start, install, setProgress } from './kit/count-down'

export default utils.uiDecorator({
  tpl: countTpl,
  count: (...args) => {
    return start(...args)
  },
  install(...args) {
    return install(...args)
  },
  setProgress
})