import assets from './assets'
import uikit from '../common/uikit'
export default function initEvent() {
  uikit.bindEvent(assets)
  uikit.initCss(assets)

  let mute = assets.get('mute') === 'true' || assets.get('mute') === true
  const localMute = window.localStorage.getItem('__262010_mute')
  if (localMute == 1) {
    mute = true
  }
  uikit.setMute(mute, false)

  let promise = Promise.resolve()
  return promise
}