export const bottomAnim = obj => AFT.animate({
  height: 0
})
  .to({
    height: 540
  })
  .duration(300)
  .update(curr => {
    obj.height = curr.get('height')
  }).play()

export const numberAnim = obj => AFT.animate({
  scale: 0.3,
  alpha: 0
})
  .to({
    scale: 1.2,
    alpha: 1
  })
  .duration(300)
  .update(curr => {
    obj.scale.set(curr.get('scale'))
    obj.alpha = curr.get('alpha')
  })
  .next()
  .to({
    scale: 1,
    alpha: 1
  })
  .duration(200)
  .update(curr => {
    obj.scale.set(curr.get('scale'))
    obj.alpha = curr.get('alpha')
  })
  .next()
  .delay(500)
  .to({
    scale: 10,
    alpha: 0
  })
  .update(curr => {
    obj.scale.set(curr.get('scale'))
    obj.alpha = curr.get('alpha')
  })
  .duration(300)
  .play()

