import { pieceAnim } from './anim'
export default class Piece {
  constructor({ x, y, rx, ry, width, height, rheight, image }) {
    this.x = x
    this.y = y
    this.sprite = {
      x: rx,
      y: ry,
      width: 111,
      height: rheight,
      image
    }
    this.rotate = 0
    this.width = width || this.sprite.width >> 1
    this.height = height || rheight >> 1
    this.anchor = [0, 0]
    this.alpha = 1
  }

  draw({ ctx, x, y, width, height }) {
    const s = this.sprite
    x = x || this.x
    y = y || this.y

    this.width = width || this.width
    this.height = height || this.height
    const [anchorX, anchorY] = this.anchor

    ctx.save()
    ctx.translate(x, y)
    ctx.rotate(this.rotate * Math.PI / 180)
    ctx.globalAlpha = this.alpha
    ctx.drawImage(s.image, s.x, s.y, s.width, s.height, -anchorX * this.width, -anchorY * this.height, this.width, this.height)
    ctx.restore()
  }

  anim() {
    return pieceAnim(this)
  }
}