/* eslint-disable no-undef */
export default class Geometry {
  constructor(prefabGeometry, buffers, count, isBufferGeometry) {
    let { keys } = buffers

    function createPrefab(geometry, count) {
      let bufferGeometry = new THREE.BAS.PrefabBufferGeometry(geometry, count, isBufferGeometry)

      let key = bufferGeometry.createAttribute('key', 1)

      return {
        geometry: bufferGeometry,
        key: key
      }
    }

    let prefab = createPrefab(prefabGeometry, count)

    for (let i = 0; i < count; i++) {
      // buffer key
      prefab.geometry.setPrefabData(prefab.key, i, keys[i])
    }

    return prefab.geometry
  }

}
