/* eslint-disable no-undef */
import constant from '../../../app/data/constant'

const { swordLength, swordThickness } = constant

export default function Animation(modelGeometry, texture) {
  let geometry = new THREE.BAS.ModelBufferGeometry(modelGeometry)
  // FIXED: must reset uvs when convert to buffer geometry
  geometry.bufferUVs()

  let key = geometry.createAttribute('key', 1)
  let ind = geometry.createAttribute('ind', 1)

  for (let i = 0, len = key.array.length; i < len; i++) {
    key.array[i] = Math.ceil(i / 2)
    ind.array[i] = i
  }

  let points = []

  for (let i = 0, len = swordLength; i < len; i++) {
    points.push(new THREE.Vector4())
  }

  let lastIndex = (swordLength - 2) * 2 + 1

  let material = new THREE.BAS.BasicAnimationMaterial({
    shading: THREE.FlatShading,
    vertexColors: THREE.VertexColors,
    transparent: true,
    uniforms: {
      points: { type: 'vec4', value: points },
      thickness: { type: 'float', value: swordThickness }
    },
    uniformValues: {
      map: texture
    },
    vertexFunctions: [
    ],
    vertexParameters: [
      `
			uniform float thickness;
			attribute float key;
			attribute float ind;
			`,
      'uniform vec4 points[' + swordLength + '];'
    ],
    vertexPosition: [
      // 'float r = thickness * sin((key / float(' + swordLength  +')) * PI);' ,
      `
			vec4 point = points[int(key)];
			float r = thickness;
			float rad = point.w + PI * 0.5;
			// up or down offset
			float offset = -1.0;
			`,

      'if (ind == 0.0 || ind ==' + lastIndex + '.0){',

      `
				offset = 0.0;
			} else if (mod(ind, 2.0) == 1.0) {
				offset = 1.0;
			}

			transformed.x = point.x + offset * cos(rad) * r;
			transformed.y = point.y + offset * sin(rad) * r;
			`
    ]
  })

  THREE.Mesh.call(this, geometry, material)
}

Animation.prototype = Object.create(THREE.Mesh.prototype)
Animation.prototype.constructor = Animation

Object.defineProperty(Animation.prototype, 'points', {
  get: function () {
    return this.material.uniforms['points'].value
  },
  set: function (v) {
    this.material.uniforms['points'].value = v
  }
})
