import { $, delay } from './utils';

import compose from 'css-mat4'
import { getPosition } from './position';


let position;

let lock = false;


export async function gainAward(id, data, awards) {

  try {

    !position && (position = getPosition(awards));

    if (lock) {
      return;
    }

    if (!position[`award_${id}`]) {
      throw new Error('没有这个奖品类型')
    }

    lock = true;

    const [targetX, targetY] = position[`award_${id}`];

    const [x, y] = data;

    const coin = $('#goldCoin')[0];

    coin.style.opacity = 0;
    let matrix = compose([], {
      translate: [x, y],
      scale: [2, 2]
    }).join(',')

    coin.style.transform = `matrix3D(${matrix})`;
    coin.style.webketTransform = `matrix3D(${matrix})`;

    // TODO: 若有性能问题 需要解决
    await delay();
    coin.style.opacity = '';
    coin.style.transform = '';
    coin.style.webkitTransform = '';
    matrix = compose([], {
      translate: [x, y],
      scale: [1, 1]
    }).join(',')

    coin.style.transform = `matrix3D(${matrix})`;
    coin.style.webketTransform = `matrix3D(${matrix})`;
    coin.style.opacity = 1;
    coin.classList.add('show');

    await delay(600);
    coin.classList.remove('show');

    matrix = compose([], {
      translate: [targetX, targetY],
      scale: [0.2, 0.2]
    });
    coin.style.transform = `matrix3D(${matrix})`;
    coin.style.webkitTransform = `matrix3D(${matrix})`;
    coin.classList.add('move');
    await delay(1000);
    coin.classList.remove('move');
    lock = false;
    coin.style.opacity = 0;
    $(`#award_${id}`)[0].classList.add('anim');
    $(`#award_${id}_Flower`)[0].classList.add('rotate');
    await delay(1000);
    $(`#award_${id}`)[0].classList.remove('anim');
    $(`#award_${id}_Flower`)[0].classList.remove('rotate');
  } catch (e) { console.log(e) }

}