import * as utils from './kit/utils'
import redpack from './tpls/game/redpackCount.tpl'
import cuttedCount from './tpls/game/cuttedCount.tpl'
import time from './tpls/game/time.tpl'
import bottom from './tpls/game/bottom.tpl'
import series from './tpls/game/series.tpl'
import goldCoin from './tpls/game/goldCoin.tpl'

import { timeStart, timePlay, timePause, timeStop } from './kit/time'
import { addCount, resetCount, getCount } from './kit/redpackCount'
import { getPosition } from './kit/position'
import { setSeries, setSeriesConfig } from './kit/series'
import { gainAward } from './kit/gainAward'

const tpl = '<div id="game-container" style="display:none">' + redpack + goldCoin + cuttedCount + time + bottom + series + '</div>'


const { $, getWindowSize } = utils

function showDom() {
  if ($('#game-container')[0]) {
    $('#game-container')[0].style.display = 'block'
  }
}


function initGame({ seriesConfig }) {

  // const { windowWidth } = getWindowSize()

  // if (windowWidth > 540) {
  //   const bottom = $('.bottom-container')[0]
  //   const rate = windowWidth / 540
  //   bottom.style.transform = `scale(${rate})`
  //   bottom.style.webkitTransform = `scale(${rate})`
  // }

  if (seriesConfig) {
    setSeriesConfig(seriesConfig)
  }

  resetCount()

}

function setAwardType(types) {
  const html = types.map(({ name, image, id }) => {
    return `<div class="item">
            <span class="flower ui-flower" id="award_${id}_Flower"></span>
            <span class="box" id="award_${id}" style="background-image:url(${image});"></span>
            <span class="word">
                ${name}
            </span>
        </div>`
  }).join('')
  $('.cut-count-container').text(html)
}



export default utils.uiDecorator({
  tpl: tpl,
  timeStart,
  addCount,
  getPosition,
  setSeries,
  gainAward,
  setAwardType,
  initGame,
  showDom,
  getCount,
  timePause,
  timePlay,
  timeStop,
})