/* eslint-disable no-undef */
import json from './cutAnim.json'

export function cutSprite(opt) {
  const image = new Image()
  image.src = opt.cutAnim
  let baseTexture = new PIXI.BaseTexture(image)

  let spritesheet = new PIXI.Spritesheet(baseTexture, json)
  let sprites = []

  spritesheet.parse(() => {
    let { textures } = spritesheet

    for (let name in textures) {
      sprites.push(textures[name])
    }
  })

  let anim = new PIXI.extras.AnimatedSprite(sprites)

  /*
   * An AnimatedSprite inherits all the properties of a PIXI sprite
   * so you can change its position, its anchor, mask it, etc
   */
  anim.x = opt.cutAnimPosition && opt.cutAnimPosition.x || 375
  anim.anchor.set(0.5)
  anim.animationSpeed = 0.15
  return anim
}
