import '../lib/three.bas/dist/bas'
import Engine from '@ali/game-engine'
import RedPacket from './RedPacket/RedPacket'
import Sword from './Sword/Sword'
import constant from '../../app/data/constant'
import state from '../../app/data/state'

export default class View {
  constructor(resources) {
    this.time = 0

    this.engine = new Engine({
      debug: constant.debug,
      renderer: {
        canvas: document.getElementById(constant.canvasId),
        // pecision: 'lowp',
        alpha: true,
        antialias: true,
      },
      camera: {
        fov: 70,
        near: 0.5,
        far: 200,
      },
      size: {
        pixelRatio: constant.isMobile ? 2 : 1,
      },
    })

    const { scene, camera } = this.engine

    camera.position.set(0, 0, 100)

    // draw the brand image to red packet texture
    const redPacketImage = this.setTexture(resources)

    // red packets particles
    this.redPacket = new RedPacket(scene, resources, redPacketImage)

    // sword
    this.sword = new Sword(scene, camera, resources)
  }

  setTexture(resources) {
    const redPacketImage = resources.images.redPacket

    if (!constant.hidePackLogo) {
      const redPacketBrandImage = resources.images.redPacketBrand
      const offscreenCanvas = document.createElement('canvas')
      offscreenCanvas.width = redPacketImage.width
      offscreenCanvas.height = redPacketImage.height

      const ctx = offscreenCanvas.getContext('2d')
      ctx.drawImage(redPacketImage, 0, 0)

      // the width and offset is from photoshop ruler
      const imageWidth = 65
      const imageOffset = [54, 65]
      try {
        ctx.drawImage(redPacketBrandImage, 0, 0, redPacketBrandImage.width, redPacketBrandImage.height, imageOffset[0], imageOffset[1], imageWidth, imageWidth)
      } catch (e) { }
      return offscreenCanvas
    }

    return redPacketImage
  }

  active() {
  }

  sleep() {
    this.engine.stop()
  }

  start() {
    this.time = 0
    this.redPacket.start()
    this.engine.start()
  }

  stop() {
    this.redPacket.stop()
    this.engine.stop()
  }

  update(delta) {
    const { intersections, cutVector, mouse, speedup } = state

    this.sword.update(delta, mouse)

    this.time += delta * speedup

    const redPackets = this.redPacket.update(delta, this.time, intersections, cutVector)

    this.engine.update(delta)

    return { redPackets }
  }
}
