/* eslint-disable no-undef */
export default class CPUPicker {

  constructor(option) {
    this.raycaster = new THREE.Raycaster()

    if (option.camera) {
      this.setCamera(option.camera)
    }

    this.tempMouse = new THREE.Vector2()
    this.width = window.innerWidth
    this.height = window.innerHeight
  }

  setCamera(camera) {
    this.camera = camera
  }

  update(mouses) {
    let { raycaster, camera, objects, tempMouse, width, height } = this

    return mouses.map((mouse) => {
      tempMouse.x = (mouse.x / width) * 2 - 1
      tempMouse.y = 1 - (mouse.y / height) * 2

      raycaster.setFromCamera(tempMouse, camera)

      let intersections = raycaster.intersectObjects(objects)
      let intersection = intersections.length > 0 ? intersections[0] : null

      if (intersection) {
        return intersection.object._key
      }
    })
  }

  addObjects(objects) {
    this.objects = objects
  }

}
