import { $, getWindowSize } from './utils'

let timmer

let config = []

export function setSeriesConfig(conf) {
  config = conf
}


export function setSeries(num, pos) {
  const { windowWidth, windowHeight } = getWindowSize()
  const moveX = 290 / 720 * windowWidth // 提示的宽度
  const moveY = 210 / 1334 * windowHeight // 提示的高度
  const moveBottomY = 290 / 1334 * windowHeight // 底部高度
  try {
    num = num > 9 ? 9 : num
    let [left, top] = pos
    left -= moveX >> 1
    top -= moveY >> 1
    if (left < 0) {
      left = 0
    }
    if (top < 0) {
      top = 0
    }

    const w = windowWidth - moveX
    if (left > w) {
      left = w
    }

    const h = windowHeight - moveBottomY - moveY
    if (top > h) {
      top = h
    }

    $('#seriesContainer')[0].style.opacity = 1
    $('#seriesContainer')[0].style.transform =
      $('#seriesContainer')[0].style.webkitTransform =
      'translate3d(' + left + 'px,' + top + 'px, 1px)'

    $('#seriesNum').attr('series-num', `series-${num}`)
    $('#seriesType').attr('series-type', getSeriesType(num))
  } catch (e) { }
  clearTimeout(timmer)
  timmer = setTimeout(() => {
    try {
      $('#seriesContainer')[0].style.opacity = 0
    } catch (e) { }
  }, 1000)
}

function getSeriesType(n) {
  const [type] = config.filter(({ num }) => {
    return parseInt(n) >= parseInt(num)
  }).sort((pre, next) => {
    if (parseInt(pre.num) < parseInt(next.num)) {
      return 1
    } else {
      return -1
    }
  })

  if (type) {
    return type.id
  } else {
    return 1
  }

}
