// 类库
import * as log from '../common/libs/log'
// 流程方法
import init from './init'
import downgrade from './downgrade'
import animation, { exportObj, pause, play, stop, stopThenPlay } from './animation'
import state from '../app/data/state'
import * as goldlog from '../common/libs/goldlog'
import uikit from '../common/uikit'
import { loadImage } from '../common/libs/loader'
window.NINJA_GAME = {
  restart() {
    if (state.stop) {
      stopThenPlay()
    } else {
      uikit.resetCache()
      Promise.resolve()
        .then(() => window.NINJA_BIZ.isAvailable())
        .then(() => {
          exportObj.restart()
        })
        .catch(e => {
          log.debug('start flow has error!', e)
        })
    }

  },
  pause,
  play,
  stop
}


const events = [
  'isAvailable',
  'gameStart',
  'lottery',
  'showResult',
  'downgrade',
  'close'
]

function checkEvent() {
  if (!window.window.NINJA_BIZ) {
    return Promise.reject('没有 window.NINJA_BIZ 对象')
  }
  const map = events.map(e => {
    if (!(window.NINJA_BIZ[e] instanceof Function)) {
      return e
    }
  }).filter(x => x)
  if (map.length > 0) {
    return Promise.reject(`请实现 window.NINJA_BIZ 的 ${map.join(',')} 方法`)
  }
  return Promise.resolve()
}

export default function NinjaInit(params) {

  const { bdbox } = params

  const container = document.querySelector(bdbox)

  container.innerHTML = `
      <div id="EleRedPacketNinja" style="position: fixed; left:0; top: 0; width: 100%; height: 100%;background: rgba(0, 0, 0, 0.88);">
      <canvas id="EleRedPacketNinjaCanvas3d"></canvas>
      <div id="EleRedPacketNinjaUI" class="ui"></div>
      <div id="EleRedPacketNinjaOperateArea" class="operate-area"></div>
    </div>
  `
  uikit.uikitInit()
  const { TPL_CONFIG } = window
  const { cutAnim, background } = TPL_CONFIG.assets
  Promise.all([
    loadImage(cutAnim),
    loadImage(background),
  ]).then(checkEvent)
    .then(init)
    .then(() => window.NINJA_BIZ.isAvailable())
    .then(downgrade)
    .then(animation)
    .then(() => {
      goldlog.flush()
      log.debug('game inited')
    })
    .catch(e => {
      log.debug('start flow has error!', e)
    })
}

NinjaInit({
  bdbox: '#demo-container'
})