let domCache = new Map();

export function resetCache() {
  domCache.clear()
}

export function getRealValue(value) {
  return parseFloat(value) / 750 * window.innerHTML
}

export async function delay(time) {
  return new Promise((resolve) => setTimeout(resolve, time));
}




export function uiDecorator(obj) {
  const tpl = obj.tpl;

  let dom;
  let parentDom;

  return {
    ...obj,
    ...{
      setup(outerDom = document.querySelector('#EleRedPacketNinjaUI')) {
        dom = document.createElement('div');
        dom.innerHTML = obj.tpl;
        parentDom = outerDom;
        outerDom.appendChild(dom);
        domCache.clear();
      },
      teardown() {
        try {
          parentDom && dom && parentDom.removeChild(dom);
        } catch (e) {
          console.log(e)
        }
        domCache.clear();
      },
      show() {
        dom.style.display = 'block'
      },
      hide() {
        dom.style.display = 'none'
      }
    }
  }
}


export function getDomPosition(el) {
  var box = el.getBoundingClientRect(),
    doc = el.ownerDocument,
    body = doc.body,
    html = doc.documentElement,
    clientTop = html.clientTop || body.clientTop || 0,
    clientLeft = html.clientLeft || body.clientLeft || 0,
    top = box.top + (self.pageYOffset || html.scrollTop || body.scrollTop) - clientTop,
    left = box.left + (self.pageXOffset || html.scrollLeft || body.scrollLeft) - clientLeft
  return [left, top];
};

const selectorFunc = {
  object(selector) {
    return [selector];
  },
  string(selector) {
    return document.querySelectorAll(selector);
  }
}


export function $(selector) {
  if (domCache.has(selector)) {
    return domCache.get(selector);
  }

  const control = [...selectorFunc[typeof selector](selector)];
  control.attr = function (key, val) {
    control.forEach((dom) => {
      dom.setAttribute(key, val);
    });
  };
  control.removeAttr = function (key) {
    control.forEach((dom) => {
      dom.removeAttribute(key);
    });
  };
  control.remove = function () {
    control.forEach((dom) => {
      dom.remove(key);
    });
  };
  control.get = function (index) {
    return $(control[index]) ? $(control[index]) : [];
  };
  control.append = function (...args) {
    control.forEach((dom) => {
      dom.append(...args);
    });
  };
  control.empty = function () {
    control.forEach((dom) => {
      dom.innerHTML = '';
    });
  };
  control.text = function (text) {
    control.forEach((dom) => {
      dom.innerHTML = text;
    });
  };
  control.addClass = function (className) {
    control.forEach((dom) => {
      dom.classList.add(className);
    });
  };
  control.removeClass = function (className) {
    control.forEach((dom) => {
      dom.classList.remove(className);
    });
  };

  domCache.set(selector, control);

  return control;
}

export function getEyeTrans({ touch, pos, r }) {
  const [x, y] = touch;
  const [x1, y1] = pos;

  const dx = x - x1;
  const dy = y - y1;

  const dr = Math.sqrt(dx * dx + dy * dy);

  const moveX = dx / dr * r;
  const moveY = moveX / (dx / dy);
  return [moveX, moveY]


  return [0, 0]

}

export function getWindowSize() {
  const windowWidth = window.innerWidth
  const windowHeight = window.innerHeight
  const baseWidth = windowWidth > 540 ? 540 : windowWidth;

  return { windowWidth, windowHeight }
}

export function appendStyle(cssText) {
  const style = document.createElement('style');
  style.textContent = cssText;
  document.getElementsByTagName('head')[0].appendChild(style);
}
