import * as utils from './utils'

const { $ } = utils


let config = {
  count: 0,
  length: 1,
  lastDiv: null,
  twoDiv: null
}

export function resetCount() {
  config = {
    count: 0,
    length: 1,
    lastDiv: null,
    twoDiv: null
  }
}
export function getCount() {
  return config.count
}
export function addCount() {
  try {
    config.count++
    const redpackCount = $('#countPackNum')[0]

    if (config.count > 100) {
      redpackCount.classList.add('three')
    }
    let lastDiv = config.lastDiv
    if (!lastDiv) {
      lastDiv = $('#firstCountAnim')[0]
    }

    if (lastDiv) {
      lastDiv.style.transform = lastDiv.style.webkitTransform = 'translate3d(0, 20px, 0) scale(0.7)'
      lastDiv.style.opacity = '0.5'
    }
    let twoDiv = config.twoDiv
    twoDiv && redpackCount.removeChild(twoDiv)

    config.twoDiv = lastDiv

    const numString = config.count.toString()

    const div = document.createElement('div')
    div.className = 'anim-box'
    for (let i = 0; i < numString.length; i++) {
      const span = document.createElement('span')
      span.setAttribute('count-num', numString[i])
      span.className = 'count'
      div.appendChild(span)
    }
    div.style.transform = div.style.webkitTransform = 'scale(0)'
    redpackCount && redpackCount.appendChild(div)
    setTimeout(() => {
      div.style.transform = div.style.webkitTransform = ''
    })
    config.lastDiv = div
  } catch (e) {
  }

}
