import { app, loader } from './canvas';
import { cutSprite } from './cutAnim';
import { $, delay } from '../utils';
import state from '../../../../../app/data/state'
import {
  foreAnim,
  zero1Anim,
  zero2Anim,
  wordAnim,
  circleAnim,
  opacityAnim,
  scaleAnim,
  cutSpriteAnim,
  bottomAnim,
  numberAnim,
  numberAnim1,
  redpackAnim
} from './anim'
import {
  backgroundPic,
  numberPic,
  startCreator
} from './creator'

import { os } from '@ali/lib-env';

import graphic, { removeProgress } from './graphic';

export { setProgress } from './graphic';

const config = {
  resource: null,
};

export function install(opt) {
  return new Promise((resolve) => {
    loader().then(() => {
      loaded(opt)
      resolve();
    })
  })
}
async function loaded(opt = {}) {
  startCreator(opt)
  const cutAnimation = cutSprite(opt);
  const background = backgroundPic();


  app.stage.addChild(background);
  app.stage.addChild(cutAnimation);
  graphic(opt, app)

  opacityAnim(background);
  cutSpriteAnim(cutAnimation, opt.cutAnimPosition && opt.cutAnimPosition.y).on('finish', () => {
    cutAnimation.play();
  });
}
export async function start(time) {
  $('.count-down-container')[0] && $('.count-down-container')[0].classList.remove('opacity');
  removeProgress();
  return new Promise(async (resolve, reject) => {
    let num;
    let count = time;
    while (count > 0) {
      if (state.stop) {
        count = 0;
        break;
      }
      num = numberPic(count);
      if (count > 1) {
        numberAnim(num);
      } else {
        numberAnim1(num);
      }
      app.stage.addChild(num);
      count--
      await delay(1000);
    }
    $('.count-down-container')[0] && $('.count-down-container')[0].classList.add('opacity');
    setTimeout(() => {
      // app.destroy(); // TODO: 打开
      // app.stage.removeChildren()
      num && app.stage.removeChild(num)
    }, 1000)
    if (state.stop) {

      reject()
    } else {
      resolve()

    }
  })
}