import Config from '../common/libs/config'
import tester from '../common/libs/tester'
import * as goldlog from '../common/libs/goldlog'

const {
  TPL_CONFIG
} = window


const config = new Config()


const {
  game,
  whitelist,
  series,
  downgrade2D,
  awardType,
  assets,
  countdown
} = TPL_CONFIG


config.set('awardType', awardType)
config.set('series', series)
config.set('downgrade2DInfo', downgrade2D)
config.set('countdownOption', countdown)

// other configs
for (const key in game) {
  config.set(key, game[key])
}

for (const key in assets) {
  config.set(key, assets[key])
}


// whitelist configs
config.set('whitelist', whitelist)


// 倒计时时间
const cdmax = parseInt(config.get('cdmax')) || 3
const cdmin = parseInt(config.get('cdmin')) || 3
let time = ~~(Math.random() * (cdmax - cdmin + 1)) + cdmin
config.set('countdown', parseInt(tester.cd) || time)


goldlog.setAssets(config)

export default config