/* eslint-disable no-undef */
import constant from '../../../app/data/constant'

export default class Material {

  constructor(isRayCaster, texture, velocities, explodeVelocities, rotations, explodeRotations, startTimes, startPositions) {
    let { redPacketNumber, redPacketAcceleration } = constant
    let M = THREE.BAS.BasicAnimationMaterial

    // to caculate the uv of texture
    let redPacketFrames = []

    for (let i = 0; i < redPacketNumber; i++) {
      // 9 is the max frame, 1 is the default frame because it's cute
      redPacketFrames.push(i < 9 ? i : 0)
    }

    let material = new M({
      vertexColors: THREE.VertexColors,
      shading: THREE.SmoothShading,
      side: THREE.DoubleSide,
      uniforms: {
        uTime: { type: 'float', value: new Float32Array(redPacketNumber) },
        acceleration: { type: 'vec3', value: new THREE.Vector3(0, redPacketAcceleration.y, 0) },
        explodes: { type: 'float', value: new Float32Array(redPacketNumber) },
        velocities: { type: 'vec3', value: velocities },
        explodeVelocities: { type: 'vec3', value: explodeVelocities },
        explodeRotations: { type: 'float', value: explodeRotations },
        rotations: { type: 'vec4', value: rotations },
        startTimes: { type: 'float', value: startTimes },
        frames: { type: 'float', value: redPacketFrames },
        startPositions: { type: 'vec3', value: startPositions }
      },
      uniformValues: {
        map: texture
      },
      vertexFunctions: [
        THREE.BAS.ShaderChunk['quaternion_rotation']
      ],
      vertexParameters: [
        `
				uniform vec3 acceleration;
				`,

        'uniform float uTime[' + redPacketNumber + '];',
        'uniform float explodes[' + redPacketNumber + '];',
        'uniform vec3 velocities[' + redPacketNumber + '];',
        'uniform vec3 explodeVelocities[' + redPacketNumber + '];',
        'uniform float explodeRotations[' + redPacketNumber + '];',
        'uniform vec4 rotations[' + redPacketNumber + '];',
        'uniform float startTimes[' + redPacketNumber + '];',
        'uniform float frames[' + redPacketNumber + '];',
        'uniform vec3 startPositions[' + redPacketNumber + '];',

        `
				attribute float key;
				`
      ],
      vertexPosition: [
        `
				int i = int(key) - 1;
				float explodeTime = explodes[i];
				vec3 startPosition = startPositions[i];
				float t = uTime[i];
				t = clamp(t, 0.0, 1.0);
				`,

        isRayCaster ?
          `
				// for raycaster
				vec4 color = vec4(key / 255.0, 1.0, 1.0, 1.0);

				// test if the framebuffer is correct
				// if (explodeTime > 0.0) {
				// 	color = vec4(1.0, 0.0, 0.0, 1.0);
				// }

				worldId = color;
				` :
          `
				// m is the sqrt of frames
				float m = 3.0;
				// float frame = floor(fract(t * 10.0) / (1.0 / pow(m, 2.0)));
				float frame = frames[i];

				float h = -floor(frame / m) * 1.0 / m;
				float w = mod(frame, m) * 1.0 / m;

				vUv = vec2(uv.x + w, uv.y + h);

				vProgress = t;
				`,

        `
				vec4 rotation = rotations[i];

				float r;

				vec3 explodeTransform = vec3(0.0);

				// explodeTime > 0.0 means is exploding
				if (explodeTime > 0.0) {
					vec3 explodeVelocity = explodeVelocities[i];
					float explodeRotation = explodeRotations[i];
					float explodeElapsedTime = t - explodeTime;
					explodeTransform = explodeVelocity * explodeElapsedTime;
					r = rotation.w + explodeRotation * explodeElapsedTime;
				} else {
					r = rotation.w * t;
				}

				vec4 quat = quatFromAxisAngle(rotation.xyz, r);
				transformed = rotateVector(quat, transformed);
				// scale
				transformed *= 4.0 + t * 2.0;

				vec3 velocity = velocities[i];

				transformed += startPosition + velocity * t + acceleration * t * t + explodeTransform;

				`
      ],
      varyingParameters: [
        isRayCaster ?
          `
				// for raycaster
				varying vec4 worldId;
				`
          :
          `
				varying float vProgress;
				`
      ],
      fragmentDiffuse: [
        // for rayster
        isRayCaster ? `
				diffuseColor = worldId;
				` : `
				// for animation
				diffuseColor /= clamp((abs(sin((vUv.x * 0.7 + vUv.y * 0.3) * 20.0 + vProgress * 15.0))), 0.5, 1.0);
				`
      ]
    })

    return material
  }
}
