import Loop from '@ali/game-loop'
import Loader from '@ali/game-loader/src/main'
import Audio from './audio/Audio'
import Game3D from '../3d/Game'
import Game2D from '../2d/Game'
import state from './data/state'
import constant from './data/constant'
import env from '@ali/lib-env'
import assets from '../template/assets'
import * as goldlog from '../common/libs/goldlog'


try {
  const game = assets.get('game')
  const { frame, backVolume, actionVolume } = game
  if (frame != '0') {
    const frameRate = parseInt(frame)
    if (!isNaN(frameRate)) {
      constant.fixedFPS = frameRate
    }
  }

  if (backVolume) {
    const volume = parseFloat(backVolume)
    if (!isNaN(volume)) {
      constant.backgroundAuidoVolume = volume
    }
  }
  if (actionVolume) {
    const volume = parseFloat(actionVolume)
    if (!isNaN(volume)) {
      constant.actionAudioVolume = volume
    }
  }
} catch (e) { }

export default class App {

  constructor(biz, downgrade2D, brandImage) {
    this.isStop = false
    let { modelSource, imageSources, imageSources2D, audioSources, fixedFPS } = constant

    if (brandImage) {
      imageSources2D.redPacketBrand = brandImage
      imageSources.redPacketBrand = brandImage
    }

    const windvane = window && window.WindVane && window.WindVane.isAvailable
    let loopConfig = {
      // IOS need app state to decide to sleep or active
      needAppState: constant.isIOS && windvane
    }

    if (fixedFPS) {
      loopConfig.fixedFPS = fixedFPS
    }

    this.loop = new Loop(loopConfig)
    this.loop.on('init', () => {

      if (!downgrade2D && !supportWebGL()) {
        downgrade2D = true
      }
      this.downgrade2D = downgrade2D

      let assets = downgrade2D ? { audioSources, imageSources: imageSources2D } : { modelSource, imageSources, audioSources }
      this.load(assets, biz, downgrade2D ? Game2D : Game3D)

    })

    this.loop.on('update', (delta) => {
      if (!this.game || this.isStop) {
        return
      }

      // if user is background before game start, start the game again
      if (this.isBackground) {
        this.start()
        this.isBackground = false
      }

      // mute state
      if (biz.getMute) {
        state.mute = biz.getMute()
      }

      // rhythm state
      if (biz.getRhythm) {
        state.rhythm = biz.getRhythm()
      }

      if (state.rhythm === 0) {
        state.backgroundAuidoRate = constant.backgroundAuidoNormalRate
        state.speedup = constant.redPacketNormalSpeed
      } else if (state.rhythm === 1) {
        state.backgroundAuidoRate = constant.backgroundAuidoSpeedUpRate
        state.speedup = constant.redPacketSpeedup
      }

      let mouses = null

      if (!state.pause || !state.stop) {
        mouses = this.game.update(delta)
      }
      // BIZ: lottery
      if (mouses) {
        mouses.forEach((mouse) => {
          if (!mouse) {
            return
          }
          // pass mouse position to biz logic
          biz.lottery([mouse.x, mouse.y])
            .then(() => {
              // console.log('lottery:', data)
            })
        })
      }
      if (this.audio) {
        this.audio.update(mouses, state.cutVector, state.mute, state.backgroundAuidoRate)
      }
    })

    this.loop.on('sleep', () => {
      this.sleeping = true
      this.game && this.game.sleep()
      this.audio && this.audio.sleep()
    })

    this.loop.on('active', () => {
      this.sleeping = false
      this.game && this.game.active()

      if (!this.ended) {
        this.audio && this.audio.active()
      }
    })

    this.loop.start()
  }

  start() {
    this.game.start()
    this.audio.start()
  }

  launch(data, biz, Game) {
    this.game = new Game(data)
    this.audio = new Audio(data, constant.backgroundAuidoVolume, constant.actionAudioVolume)
    function start() {
      // BIZ: game start
      biz.start().then(() => {
        this.ended = false

        if (this.downgrade2D) {
          goldlog.record('EXP', 'event=game_start&gameType=2d&')
        } else {
          goldlog.record('EXP', 'event=game_start&gameType=3d&')
        }

        // if user is background before game start, try start the game later
        if (this.sleeping) {
          this.isBackground = true
        } else {
          this.start()
        }

        return biz.running()
      })
        // BIZ: game end
        .then(restart => {
          this.game.stop()

          if (!this.sleeping) {
            this.audio.stop()
          }

          this.ended = true

          return restart()

        })
        // BIZ: restart
        .then(() => {
          start()
        })
    }

    // eslint-disable-next-line no-func-assign
    start = start.bind(this)
    start()
  }

  load(assets, biz, Game) {
    let loader = new Loader(assets)

    loader.on('modelProgress', (progress) => {
      // model occupy 50% percent
      biz.setProcess(
        (progress.loaded / progress.total * 50) >> 0
      )
    })

    loader.on('assetsProgress', (progress) => {
      // assets occupy 50% percent
      biz.setProcess(
        50 + ((progress.loaded / progress.total * 50) >> 0)
      )
    })

    loader.on('assetsLoaded', (data) => {
      try {
        if (this.srcError && this.srcError.length > 0) {
          postError(this.srcError)
          return
        }
      } catch (e) {
        console.log(e)
      }
      this.launch(data, biz, Game)
    })

    loader.on('modelLoadError', () => {

      // TODO: load error UI
      if (!this.srcError) {
        this.srcError = []
      }
      try {
        this.srcError.push('modelError')
        postError(this.srcError)
      } catch (e) { }
    })

    loader.on('assetsLoadError', (e) => {
      // TODO: load error UI
      if (e.resource.getName().indexOf('.mp3') > -1) {
        return
      }
      if (this.downgrade2D) {
        if (!this.srcError) {
          this.srcError = []
        }
        try {
          this.srcError.push(e.resource.getName())
        } catch (e) { }
      }
    })

    loader.start()
  }
  pause() {
    this.loop.sleep()
  }
  play() {
    this.loop.active()
  }
  stop() {
    this.isStop = true
    this.pause()
    this.game.stop()
    this.audio.stop()
  }
  clear() {
    this.game.clear()
  }
}
function postError(srcError) {
  try {
    const browser = JSON.stringify(env.browser)
    const errSrc = JSON.stringify(srcError)
    const app = JSON.stringify(env.aliapp)
    window.WindVane.call('WVNetwork', 'getNetworkType', {}).then(({ type }) => {
      const c = `error=${errSrc}&network=${type}&web=${browser}&uacore=${app}`
      window.goldlog && window.goldlog.record('/mfe.4.1602', 'CLK', c, 'H46747614')
    }).catch(() => {
      const c = `error=${errSrc}&web=${browser}&uacore=${app}`
      window.goldlog && window.goldlog.record('/mfe.4.1602', 'CLK', c, 'H46747614')
    })
    toastError()
  } catch (e) {
    throw e
  }
}
function supportWebGL() {
  const canvas = document.createElement('canvas')
  var names = ['webgl', 'experimental-webgl']
  var context = null

  for (var i = 0; i < names.length; i++) {
    try {
      context = canvas.getContext(names[i])
    } catch (e) { }

    if (context) {
      return true
    }
  }
}
function toastError() {

  // const div = document.createElement('div')
  // div.className = 'toast-container'
  // div.innerHTML = assets.get('loadError') || '切红包加载失败，退出重试哦'
  // document.body.appendChild(div)
  // setTimeout(() => {
  //   document.body.removeChild(div)
  // }, 100000)
  throw new Error('切红包加载失败，退出重试哦')

}
