/* eslint-disable no-undef */
import Animation from './Animation'
import constant from '../../../app/data/constant'

const { swordLength } = constant

/**
 * @class Sword
 *
 * inspired by https://codepen.io/soulwire/pen/Ckzen
 *
 * the geometry is generated by code, like this:
 *
 *		._._._._._._._.
 * ./ . . . . . . . .\.
 *  \	._._._._._._._./
 *
 * Sadly threejs will dipuliate the vertices of geometry when convert to bufferGeometry, that's why i write the code
 *
 */
export default class Sword {

  constructor(scene, camera, resources) {
    this.scale = 0.01
    this.width = window.innerWidth * this.scale
    this.height = window.innerHeight * this.scale
    this.z = camera.position.z - this.height * 0.5 / Math.tan(camera.fov / 180 * Math.PI * 0.5)

    this.texture = new THREE.Texture(resources.images['claw'])
    this.texture.needsUpdate = true

    let points = this.points = []

    for (let i = 0, len = swordLength; i < len; i++) {
      points.push(new THREE.Vector2(0, 0))
    }
    let geometry = this.triangulate(points)

    this.mesh = new Animation(geometry, this.texture)
    this.mesh.translateZ(this.z)
    scene.add(this.mesh)
  }

  triangulate(points) {
    let vertices = []
    let indices = []
    let uvs = []

    for (let i = 0, len = points.length; i < len; i++) {
      let p = points[i]

      if (i === 0 || i === len - 1) {
        vertices.push([p.x, p.y])
        uvs.push([i / (len - 1), 0.5])
      } else {
        vertices.push([p.x, p.y])
        vertices.push([p.x, p.y])
        uvs.push([i / (len - 1), 1])
        uvs.push([i / (len - 1), 0])
      }
    }
    for (let i = 0, len = vertices.length; i < len - 2; i++) {
      if (i % 2) {
        indices = indices.concat([i, i + 1, i + 2])
      } else {
        indices = indices.concat([i, i + 2, i + 1])
      }
    }

    let geometry = new THREE.Geometry()

    for (let i = 0, len = vertices.length; i < len; i++) {

      let vert = vertices[i]
      geometry.vertices.push(new THREE.Vector3(vert[0], vert[1], 0))
    }

    for (let i = 0, len = indices.length; i < len - 2; i += 3) {
      let a = indices[i]
      let b = indices[i + 1]
      let c = indices[i + 2]
      geometry.faces.push(new THREE.Face3(a, b, c))
      geometry.faceVertexUvs[0].push([
        new THREE.Vector2(uvs[a][0], uvs[a][1]),
        new THREE.Vector2(uvs[b][0], uvs[b][1]),
        new THREE.Vector2(uvs[c][0], uvs[c][1])
      ])
    }

    return geometry
  }

  addPoint(point) {
    if (!point) {
      return
    }

    if (this.points.length >= swordLength) {
      this.points.shift()
    }

    this.points.push(new THREE.Vector2(
      (point.x * this.scale - this.width * 0.5),
      (-point.y * this.scale + this.height * 0.5)
    ))
  }


  resetPoints(point) {
    let { width, height, scale, points } = this

    for (let i = 0, len = points.length; i < len; i++) {
      points[i] = new THREE.Vector2(
        (point.x * scale - width * 0.5),
        (-point.y * scale + height * 0.5)
      )
    }
  }

  update(delta, mouse) {
    if (this.mesh) {

      if (this.starting) {
        this.resetPoints(mouse)
        this.starting = false
      }


      mouse && this.addPoint(mouse)

      let points = this.mesh.points
      let uniformLength = points.length
      let len = this.points.length

      let startPoint = this.points[0]
      let endPoint = this.points[len - 1]
      let distance = startPoint.distanceTo(endPoint)

      let heading

      // when the distance is too small, just get the heading from end point to start point
      if (distance < this.scale * 20) {
        // atan2 to calculate the orientation of heading
        heading = Math.atan2(endPoint.y - startPoint.y, endPoint.x - startPoint.x)
      }

      for (let i = 0; i < len; i++) {
        let currentPoint = this.points[len - 1 - i]
        let lastPoint

        if (len - 2 - i >= 0) {
          lastPoint = this.points[len - 2 - i]
          // save the heading to keep the texture uv correct
          if (heading === undefined) {
            heading = Math.atan2(currentPoint.y - lastPoint.y, currentPoint.x - lastPoint.x)
          }
        }

        points[uniformLength - 1 - i].x = currentPoint.x
        points[uniformLength - 1 - i].y = currentPoint.y
        // save the heading in w channel
        points[uniformLength - 1 - i].w = heading
      }
      // console.log(heading);
    }
  }

  stop() {
  }

  start() {
    this.starting = true
  }
}
