import assets from './assets'
import uikit from '../common/uikit'
import tester from '../common/libs/tester'
// let index = 0;
const map = new Map()

function setSeries(num) {

  map.forEach(([time]) => clearTimeout(time))
  const arr = [...map.values()]
  map.clear()
  uikit.setSeries(num, arr[arr.length - 1][1])
}
export function checkSeries(data) {
  let time = parseFloat(tester.seriesTime) || parseFloat(assets.get('seriesTime'))
  time *= 1000
  const timmer = setTimeout(() => {
    if (map.size >= 3 && map.size < 4) {
      setSeries(map.size)
    } else if (map.size >= 4 && map.size < 5) {
      setSeries(map.size)
    } else if (map.size >= 5) {
      setSeries(map.size)
    } else {
      map.delete(data)
    }
  }, time || 500)

  const series = [timmer, data]
  map.set(data, series)
}