import './ui/css/index.less'
import './ui/css/game.less'

import gameTpl from './ui/game'
import countDown from './ui/countDown'
import operate from './ui/operate'
import * as utils from './ui/kit/utils'

function uikitInit() {
  operate.setup(document.querySelector('#EleRedPacketNinjaOperateArea'))
  const operateArea = document.querySelector('#EleRedPacketNinjaOperateArea')
  if (operateArea) {
    operateArea.style.opacity = 0
    setTimeout(() => {
      operateArea.style.transition = 'all 1s'
      operateArea.style.webkitTransition = 'all 1s'
      operateArea.style.opacity = 1
    }, 10)
  }
  // 防止猫客poplayer上下滑动
  document.querySelector('#EleRedPacketNinja').addEventListener('touchmove', e => e.preventDefault())

}
function initCss(assets) {

  const seriesImage = assets.get('seriesImage')
  const operate = assets.get('operate')
  const bottom = assets.get('bottom')
  const count = assets.get('count')
  const gold = assets.get('gold')
  const flower = assets.get('flower')
  let cssText = ''
  if (operate) {
    cssText += `
            .index-icon {
                background-image: url(${operate});
            }
        `
  }
  if (seriesImage) {
    cssText += `
            .series-icon {
                background-image: url(${seriesImage});
            }
        `
  }
  if (bottom) {
    cssText += `
            .bottom-container .head {
                background-image: url(${bottom});
            }
        `
  }
  if (count) {
    cssText += `
            .redpack-count-container .count {
                background-image: url(${count});
            }
        `
  }
  if (gold) {
    cssText += `
            .gold-coin-container .coin {
                background-image: url(${gold});
            }
        `
  }
  if (flower) {
    cssText += `
            .cut-count-container .flower {
                background-image: url(${flower});
            }
        `

  }

  cssText && utils.appendStyle(cssText)
  // window.WindVane.call('WVNativeDetector', 'getModelInfo', {}).then(e => {
  //   console.log(window.WindVane.call('WVNativeDetector', 'getModelInfo', {}))
  //   if (e.platform == 'iPhone10,3' || e.platform == 'iPhone10,6') {
  //     utils.appendStyle(`
  //               .safe-area {
  //                   margin-top: 44px!important;
  //               }
  //           `)
  //   }
  // })

}


export default {
  uikitInit() {
    uikitInit()
  },
  start(time) {
    gameTpl.showDom()
    return gameTpl.timeStart(time)
  },
  pause() {
    return gameTpl.timePause()
  },
  play() {
    return gameTpl.timePlay()
  },
  uiStop() {
    return gameTpl.timeStop()
  },
  hideGame() {
    gameTpl.teardown()
  },
  installEntry(obj) {
    countDown.setup()
    return countDown.install(obj)
  },
  countDown(...args) {
    countDown.show()
    setTimeout(() => gameTpl.setup(), 1000) // 预先安装游戏界面
    return countDown.count(...args).then(() => {
      setTimeout(() => countDown.hide(), 1000)
    })
  },
  getCount() {
    return gameTpl.getCount()
  },
  getPosition() {
    gameTpl.getPosition()
  },
  gainAward(...arg) {
    gameTpl.gainAward(...arg)
  },
  addCount() {
    gameTpl.addCount()
  },
  setProcess(num) {
    countDown.setProgress(num)
  },
  setSeries(...arg) {
    gameTpl.setSeries(...arg)
  },
  initGame(...args) {
    gameTpl.initGame(...args)
  },
  getMute() {
    return operate.getMute()
  },
  setMute(...flag) { // flag, localstorage
    operate.setMute(...flag)
  },
  bindEvent(...args) {
    operate.bindEvent(...args)
  },
  setAwardType(...args) {
    gameTpl.setAwardType(...args)
  },
  initCss(assets) {
    initCss(assets)
  },
  resetCache() {
    utils.resetCache()
  }
}
