import Piece from './Piece'
import constant from '../app/data/constant'
import { getWindowSize } from '../common/uikit/ui/kit/utils'
const redpackCache = [];
let first = true;
let LOGO_PACK_ID = 2;
let logoRedpack;

export function createLogoRedpack(texture) {
  const canvas = document.createElement('canvas');
  canvas.width = 111;
  canvas.height = 147;
  const ctx = canvas.getContext('2d')
  ctx.drawImage(
    texture.redPack,
    0,
    LOGO_PACK_ID * 147,
    111,
    147,
    0,
    0,
    111,
    147);
  try {
    ctx.drawImage(
      texture.redPacketBrand,
      15,
      54,
      75,
      75
    )
  } catch (e) { }
  logoRedpack = canvas;

}

export default function createRedpack(...arg) {
  let redpack = redpackCache.pop();
  if (!redpack) {
    redpack = new Redpack(...arg);
  }
  redpack.get();
  redpack.x = arg[0].x || redpack.x;
  return redpack;
}

class Redpack {
  constructor({
    x = getWindowSize().width >> 1,
    y = getWindowSize().height,
    id,
    width,
    height,
    image
  }) {
    this._x = x;
    this._y = y;
    const random = ~~(Math.random() * 9)
    this.id = id ? id % 8 + 1 : random;
    if (this.id == LOGO_PACK_ID && !constant.hidePackLogo) {
      this.sprite = {
        name: `redpack${random}`,
        image: logoRedpack,
        x: 0,
        y: 0,
        width: 111,
        height: 147
      };
      this.id = 0;
    } else {
      this.sprite = {
        name: `redpack${random}`,
        image: image,
        x: 0,
        y: 147 * random,
        width: 111,
        height: 147
      };
    }
    this.width = width || this.sprite.width >> 1;
    this.height = height || this.sprite.height >> 1;
    this.x = x;
    this.y = y;
    this.anchor = [0.5, 0.5];
    this.using = false;
    this.rotate = 45;
    this.anim = [];
    this.cutted = false;
    this.piece = [];
    this.alpha = 1;
  }
  draw({ ctx, x, y, width, height }) {
    const s = this.sprite;
    x = x || this.x;
    y = y || this.y;

    this.width = width || this.width;
    this.height = height || this.height;
    const [anchorX, anchorY] = this.anchor;

    x = x;
    y = y;
    ctx.save();
    ctx.translate(x, y);
    ctx.rotate(this.rotate * Math.PI / 180)
    ctx.globalAlpha = this.alpha;
    ctx.drawImage(s.image, s.x, s.y, s.width, s.height, -anchorX * this.width, -anchorY * this.height, this.width, this.height);
    ctx.restore();

  }
  cut() {
    if (!this.cutted) {
      this.cutted = true;
      this.boom();
      this.anim.forEach(a => {
        a.stop();
      });
    }
  }

  get() {
    this.using = true;
    this.x = this._x;
    this.y = this._y;
  }

  over() {
    this.using = false;
    this.anim = [];
  }

  cutOver() {
    this.cutted = false;
    redpackCache.push(this);
  }

  check(pos) {
    const { width, height } = this;
    const r = (height / 2 + width / 2) / 2;
    const range = r * r;
    const { x, y } = this;
    const [x1, y1] = pos;


    const distance = (x - x1) * (x - x1) + (y - y1) * (y - y1)
    if (distance > range) {
      return false;
    }
    return true

  }

  boom() {
    const cutLine = Math.random() * 0.3 + 0.3;
    if (this.piece.length == 0) {
      const config = {
        width: this.width,
        x: this.x,
        y: this.y,
        rx: 0,
        image: this.sprite.image
      }
      const piece1 = {
        height: cutLine * this.height,
        ry: this.sprite.y,
        rheight: cutLine * this.sprite.height,
        type: 1
      }
      const piece2 = {
        ry: this.sprite.y + cutLine * this.sprite.height,
        height: this.height - cutLine * this.height,
        rheight: 147 - cutLine * this.sprite.height,
        type: 2
      }
      this.piece.push(new Piece({ ...piece1, ...config }));
      this.piece.push(new Piece({ ...piece2, ...config }));
    }
    this.piece[0].anchor = [0.5, 1];
    this.piece[1].anchor = [0.5, 0];
    this.piece[0].type = 1;
    this.piece[1].type = 2;
    const promises = this.piece.map(x => {
      x.rotate = this.rotate;
      x.x = this.x;
      x.y = this.y;
      x.alpha = 1;

      return x.anim()
    })
    Promise.all(promises).then(() => {
      this.cutOver();
    });
  }
  drawPriece(...args) {
    this.piece[0].draw(...args);
    this.piece[1].draw(...args);
  }
}
