import * as utils from './kit/utils'
import operateTpl from './tpls/operate.tpl'

const { $ } = utils


let mute = false


function bindEvent() {
  const closeDom = $('.close-confirm')[0]
  if (closeDom) {
    closeDom.addEventListener('touchstart', (e) => {
      e.stopPropagation()
      e.cancelBubble = true
    })
    closeDom.addEventListener('click', () => {
      window.NINJA_BIZ.close()
    })
  }



  const muteDom = $('.mute-container')[0]
  if (muteDom) {
    muteDom.addEventListener('touchstart', (e) => {
      e.stopPropagation()
      e.cancelBubble = true
    })
    muteDom.addEventListener('click', () => {
      setMute(!mute)
    })
  }

}


function getMute() {
  return mute
}

function setMute(flag, local = true) {
  const muteDom = $('.mute-container')[0]
  mute = flag
  if (local) {
    window.localStorage.setItem('__171111_mute', mute ? 1 : 0)
  }
  if (mute) {
    muteDom && muteDom.classList.add('index-voiceoff')
    muteDom && muteDom.classList.remove('index-voiceon')
  } else {
    muteDom && muteDom.classList.remove('index-voiceoff')
    muteDom && muteDom.classList.add('index-voiceon')
  }
}



export default utils.uiDecorator({
  tpl: operateTpl,
  bindEvent,
  getMute,
  setMute
})