import state from '../data/state'
export default class Audio {

  constructor(resources, backgroundVolume, actionVolume) {
    // background music
    this.backgroundAudio = resources.audios.background.loop(true)
    this.cheerAudio = resources.audios.cheer
    this.hitAudio = resources.audios.hit
    this.hitBackAudio = resources.audios.hitBack

    // keep the background music down
    this.backgroundAudio.volume(backgroundVolume)
    this.cheerAudio.volume(backgroundVolume)
    this.hitAudio.volume(actionVolume)
    this.hitBackAudio.volume(actionVolume)

    this.mute = false
    this.rate = 1
  }

  active() {
    this.play()
  }

  sleep() {
    this.backgroundAudio.pause()
  }

  play() {
    if (!this.running || this.mute) {
      return
    }

    if (!this.backgroundAudio.playing()) {
      this.backgroundAudio.play()
    }
  }

  start() {
    this.backgroundAudio.fade(0, 1.0, 1000)
    this.running = true
    this.play()
  }

  pause() {
    this.backgroundAudio.pause()
    this.hitBackAudio.pause()
    this.hitAudio.pause()
    this.cheerAudio.pause()
  }

  stop() {
    if (!this.mute) {
      // fade the background music and cheer!
      this.backgroundAudio.fade(1.0, 0, 1000)
      !state.stop && this.cheerAudio.play()
    }
  }

  update(redPackets, cutVector, mute, backgroundAuidoRate) {
    if (this.mute !== mute) {
      this.mute = mute

      if (this.mute === true) {
        this.pause()
      } else {
        this.play()
      }
    }

    if (this.mute === true) {
      return
    }

    // speed up slowly
    if (this.rate < backgroundAuidoRate) {
      this.rate += 0.002
      this.backgroundAudio.rate(this.rate)
    }

    if (redPackets && (redPackets[0] || redPackets[1])) {
      // different cut orientation different sound
      if (cutVector.x > 0) {
        this.hitBackAudio.play()
      } else {
        this.hitAudio.play()
      }
    }
  }
}
