import ee from 'event-emitter'
import PxLoader from 'pxloader'
import './PxLoaderAudio'
import 'pxloader/PxLoaderImage'

class Loader {
	constructor (config) {
		this.modelSource = config.modelSource || null
		this.imageSources = config.imageSources  || null
		this.audioSources = config.audioSources || null
		this.materials = {}

		this.smoothModelProgress = this.modelProgress = 0
		this.smoothAssetsProgress = this.assetsProgress = 0
	}

	start (lod) {
		if (lod !== undefined) {
			this.lod = lod
		}

		if (this.modelSource) {
			this.loadModel()
		} else {
			this.loadAssets()
		}
	}

	update (delta) {
		if (this.smoothAssetsProgress === 1) {
			return false
		}

		this.smoothModelProgress += (this.modelProgress - this.smoothModelProgress) * delta
		this.smoothAssetsProgress += (this.assetsProgress - this.smoothAssetsProgress) * delta

		// if the progress > 0.999, finish the progress.
		if (this.smoothModelProgress > 0.999) {
			this.smoothModelProgress = 1
		}

		if (this.smoothAssetsProgress > 0.999) {
			this.smoothAssetsProgress = 1
		}

		return true
	}

	loadModel () {

		let modelLoader = new THREE.ObjectLoader()

		let onLoad = (model) => {
			this.model = model

			this.emit('modelLoaded', {
				model: model,
				materials: this.materials
			})

			let hasMaterial = false

			for (let prop in this.materials) {
				if(this.materials[prop]){
					hasMaterial = true
					
					break
				}
			}

			// It's possible to replace pure color material when the model is loaded
			if (hasMaterial) {
				this.replaceMaterials()
			}

			this.loadAssets()
		}

		let onProgress = (xhr) => {
			if (xhr.total > 0) {
				this.modelProgress = xhr.loaded / xhr.total

				this.emit('modelProgress', {
					loaded: xhr.loaded,
					total: xhr.total
				})
			}
		}

		let onError = (xhr) => {
			this.emit('modelLoadError', xhr)
		}

		modelLoader.load(this.modelSource, onLoad, onProgress, onError)
	}

	loadAssets () {
		let assetsLoader = new PxLoader()

		// load images
		let images = {}

		for (const prop in this.imageSources) {
			let imageSources = this.imageSources[prop]

			let img = (typeof this.lod === 'number' ? imageSources[this.lod] : imageSources)

			// cors
			images[prop] = assetsLoader.addImage(img, undefined, undefined, {
				origin: 'anonymous'
			})
		}

		// load audios
		let audios = {}

		for (const prop in this.audioSources) {
			let audio = this.audioSources[prop]
			audios[prop] = assetsLoader.addAudio(audio)
		}

		assetsLoader.addProgressListener((xhr) => {
			this.assetsProgress = xhr.completedCount / xhr.totalCount
			if (xhr.error || xhr.timeout) {
				this.emit('assetsLoadError', xhr)
			} else {
				this.emit('assetsProgress', {
					loaded: xhr.completedCount,
					total: xhr.totalCount
				})
			}
		})

		assetsLoader.addCompletionListener(() => {
			if (images) {
				this.emit('imagesLoaded', {
					images: images,
					materials: this.materials
				})

				this.replaceMaterials()
			}

			this.emit('assetsLoaded', {
				model: this.model,
				materials: this.materials,
				audios: audios,
				images: images
			})
		})

		assetsLoader.start()
	}

	replaceMaterials () {
		// if there is no model, nothing to replace
		if (!this.model) {
			return
		}

		this.model.traverse((child) => {
			let {material} = child


			if (!material){
				return
			}

			let {name} = material
			
			if(this.materials[name]){
				child.material = this.materials[name]
			}
		})
	}
}

ee(Loader.prototype)

export default Loader
