import Touch from './touch'
import constant from '../../app/data/constant'

const { isMobile } = constant

export default class Control {

  constructor(camera, renderer) {
    // FIXED: we should not bind the touchmove event on document or body, because Chrome treat touchmove event on both elements as a passive event, which is a bug. If the passive event was passed to the UC core, UC will slow down the FPS on low level Android system (eg. v4.4.4), which is anothor pit.
    let container = document.querySelector('#EleRedPacketNinja')

    this.touch = new Touch(container, camera, renderer, isMobile)
  }

  update() {
    this.intersections = this.touch.update()
  }

  start() {
    this.touch.start()
  }

  stop() {
    this.touch.stop()
  }
}
