
export function getModel() {
  return window.WindVane.call('WVNativeDetector', 'getModelInfo', {})
    .then(e => {
      var model = ''
      if (window.lib.env.os.isIOS && e.platform) {
        model = e.platform
        // eslint-disable-next-line no-undef
      } else if (lib.env.os.isAndroid && e.model) {
        model = e.model
      }
      return model
    })
    .catch(() => {
      return 'unkown'
    })
}




